.cargas {
  min-height: 300px;
  color: white;
  background-image: url("../img/bg-cargas.jpg");
  background-size: 100% auto;
  overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 20px;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: $guava;
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
        max-width: 800px;
      }
    }
  }
  &--item {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}
.container--item {
  .item-title {
    margin-bottom: 30px;
    color: $blue;
    font-size: 36px;
    font-weight: 300;
  }
}
.table-specs {
  tbody {
    tr:first-child {
      td {
        border: 0;
      }
    }
    td {
      padding-top: 15px;
      padding-bottom: 15px;
      color: black;
      font-weight: 300;

      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }
  }
  strong {
    display: block;
  }
  small {
    font-size: 14px;
  }
}
@media (min-width: 1200px) {
  .table-specs {
    strong {
      white-space: nowrap;
    }
  }
}
@media (max-width: 991px) {
  .cargas {
    background-size: auto 100%;

    &--title {
      font-size: 32px;
    }
    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
    &--item {
      .pt-2.pb-2 {
        order: 2;
      }
    }
  }
  .table-specs {
    tbody {
      td {
        padding: 10px 5px;
        font-size: 14px;
      }
    }
    small {
      font-size: 12px;
    }
  }
}