.sustentabilidade {
  &-infografico {
    h3 {
      margin-top: 20px;
      color: $guava;
      font-weight: 300;
    }
    p:last-child {
      margin-bottom: 0;
    }
    .blue {
      color: $blue;
    }
    &--img {
      display: block;
      padding-bottom: 6px;
      margin: 40px 0;
      text-align: center;
      border-bottom: 3px solid $blue;

      img {
        max-width: 100%;
      }
    }
  }
  &-futuro {
    padding: 60px 0;
    text-align: center;
    background-color: #e7e7e7;

    .img-responsive {
      padding: 40px 0;
    }
  }
}
.descarbo {
  min-height: 300px;
  color: white;
  background-image: url("../img/bg-descarbo.png");
  background-size: 100% auto;
  overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 20px;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: $guava;
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
        max-width: 800px;
      }
    }
  }
}
@media (max-width: 1199px) {
  .descarbo {
    background-size: auto 100%;

    &--title {
      font-size: 32px;
    }
    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
  }
}
@media (min-width: 576px) and (max-width: 767px) {
  .sustentabilidade {
    &-infografico {
      padding-top: 25px;

      h3 {
        color: $guava;
        font-weight: 300;
      }
      p {
        font-size: 15px;
      }
      .row {
        margin: 0 -10px;
      }
      .col-24 {
        padding: 0 10px;
      }
      &--img {
        margin: 20px 0 30px;

        img {
          max-width: 100%;
        }
      }
    }
  }
}