body {
  font-family: $font-default;
  color: $font-color;
	overflow-x: hidden;

	&.opened-menu {
		overflow-y: hidden;
	}
}
.main--intro {
	text-align: center;

	>.container {
		@media (min-width: 992px) {
			max-width: 800px;
		}
	}
}
.section-title {
	margin-top: 15px;
	margin-bottom: 30px;
	color: $blue;
	font-size: 36px;
}
.title {
	margin-top: 0;
	margin-bottom: 30px;
	color: $navy;
	font-size: 36px;
	font-weight: 300;

	@media (max-width: 575px)  {
		font-size: 32px;
	}
}
.img {
	img {
		width: 100%;
	}
}
.img-responsive {
	img {
		max-width: 100%;
	}
}
.fullbanner {
	position: relative;

	&--txt {
		position: absolute;
		top: 10%;
		left: 50%;
		transform: translateX(-50%);
		width: 400px;
		max-width: 100%;
		padding: 0 15px;
		text-align: center;
	}
	&--title {
		margin-bottom: 30px;
		color: white;
		font-size: 36px;
		font-weight: 300;
		text-align: center;
		line-height: 1.2;
	}
	&--img {
		img {
			width: 100%;
		}
	}
}
.inovacao {
	padding: 40px 0 60px;

	&--title {
		margin-bottom: 30px;
		color: $navy;
		font-size: 36px;
		font-weight: 300;
		text-align: center;

		@media (max-width: 767px) {
			font-size: 32px;
		}
	}
	&--subtitle {
		max-width: 645px;
		margin: 0 auto 20px;
		text-align: center;
	}
}

@media (min-width: 1800px) {
	.fullbanner {
		&--txt {
			width: 460px;
		}
		&--title {
			font-size: 42px;
		}
	}
}
@media (max-width: 575px) {
	.fullbanner {
		&--title {
			font-size: 32px;
		}
		&--img {
			height: 400px;
			overflow: hidden;

			img {
				width: auto;
				height: 100%;
			}
		}
	}
}