.fatos {
  position: relative;
  min-height: 700px;
  overflow: hidden;

  &--wrap {
    position: absolute;
    top: 12%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    color: white;

    p {
      text-align: center;
    }
  }
  &--title {
    margin-top: 0;
    margin-bottom: 30px;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--list {
    width: 700px;
    margin: 0 auto 30px;

    .list-row {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
    }
    .list-item {
      flex: 0 0 25%;
      padding: 10px;
      text-align: center;
      white-space: nowrap;

      p {
        margin-bottom: 0;
      }
    }
    .item-number {
      display: block;
      color: $guava;
      font-size: 42px;
      font-weight: 300;
    }
  }
  img {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;

    @media (min-width: 1580px) {
      width: 100%;
      height: auto;
    }
  }
}
@media (max-width: 767px) {
  .fatos {
    &--wrap {
      top: 8%;
      width: 100%;
    }
    &--list {
      width: 100%;

      .list-item {
        font-size: 14px;
      }
      .item {
        &-number {
          font-size: 36px;
        }
      }
    }
  }
}
@media (max-width: 575px) {
  .fatos {
    &--wrap {
      top: 30px;
      width: 100%;
    }
    &--title {
      margin-bottom: 10px;
      font-size: 32px;
    }
    &--list {
      // padding: 0 15px;
      margin-bottom: 10px;

      .list-item {
        flex: 0 0 33.3333%;
        // padding: 5px;
        font-size: 13px;
      }
      .item-number {
        font-size: 30px;
      }
    }
  }
}