.ferroviario {
  min-height: 300px;
  color: white;
  background-image: url("../img/bg-ferroviario.png");
  background-size: 100% auto;
  overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 0;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
        max-width: 800px;
      }
    }
  }
  ol {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin: 0 -15px;
    list-style: none;
    // counter-reset: counter;

    li {
      flex: 0 0 50%;
      display: flex;
      padding: 25px 15px 0;
      // counter-increment: counter;

      &:before {
        // content: counter(counter);
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 28px;
        height: 28px;
        min-width: 28px;
        margin-right: 15px;
        color: $navy;
        font-size: 20px;
        background-color: white;
        border-radius: 100%;
      }
      &:nth-child(1) {
        &:before {
          content: "1";
        }
      }
      &:nth-child(2) {
        &:before {
          content: "4";
        }
      }
      &:nth-child(3) {
        &:before {
          content: "2";
        }
      }
      &:nth-child(4) {
        &:before {
          content: "5";
        }
      }
      &:nth-child(5) {
        &:before {
          content: "3";
        }
      }
      &:nth-child(6) {
        &:before {
          content: "6";
        }
      }
    }
  }

  &-desova {
    padding-top: 60px;
    text-align: center;

    &--title {
      margin-bottom: 40px;
      color: $navy;
      font-size: 36px;
      font-weight: 300;
    }
    p {
      max-width: 650px;
      margin: 0 auto;
    }
    .color {
      color: $guava;
    }
  }
}
@media (max-width: 1199px) {
  .ferroviario {
    background-size: auto 100%;

    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
  }
}
@media (max-width: 575px) {
  .ferroviario {
    &--title {
      font-size: 30px;
    }
    ol {
      li {
        flex: 0 0 100%;
        display: flex;
        padding: 15px 15px 0;
      }
    }
  }
}