.conheca {
  padding-top: 50px;
  text-align: center;

  &--title {
    margin: 0 0 30px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  p {
    margin-bottom: 30px;
  }
  &--infografico {
    padding: 40px 15px 60px;
    margin-top: 50px;
    text-align: center;
    background-image: url("../img/bg-conheca.jpg");
    background-attachment: fixed;
    background-size: auto;
    background-position: bottom center;
    background-repeat: no-repeat;

    img {
      max-width: 100%;
    }
  }
}
@media (min-width: 1500px) {
  .conheca {
    p {
      font-size: 18px;
    }
    .btn {
      font-size: 18px;
    }
  }
}
@media (max-width: 767px) {
  .conheca {
    padding: 30px 0 0;

    &--infografico {
      padding: 0 15px;
      // background-size: auto 100%;
    }
  }
}