.contato {
  padding: 60px 0;

  &--title {
    margin-top: 0;
    margin-bottom: 40px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--list {
    display: flex;
    justify-content: space-between;

    .item {
      flex: 0 0 11%;
      padding-top: 20px;
      transition: all .3s;

      &-wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100%;
        padding: 25px 15px;
        color: $navy;
        line-height: 1.2;
        text-align: center;
        border-radius: 10px;
        box-shadow: 0 0 10px rgba(#282828, .1);
        transition: all .3s;
      }
      .icon {
        margin-bottom: 10px;
        color: $blue;
        font-size: 50px;
      }
      &:hover {
        padding-top: 0;
        padding-bottom: 20px;
        text-decoration: none;

        .item-wrap {
          color: white;
          background-color: $navy;
        }
      }
    }
  }
  &-escritorios {
    min-height: 300px;
    color: white;
    background-image: url("../img/bg-water.jpg");
    background-size: 100% auto;
    overflow: hidden;

    >.container-fluid {
      min-height: 300px;

      >.row {
        min-height: 300px;

        >.col-24 {
          &:last-child {
            align-self: center;
            max-width: 800px;
            padding: 30px 15px;
          }
        }
      }
    }
    &--title {
      margin-bottom: 15px;
      color: $guava;
      font-size: 36px;
      font-weight: 300;
      line-height: 1;
    }
    .img-responsive {
      margin-bottom: 0;
    }
    p {
      &:last-child {
        margin-bottom: 0;
      }
    }

    .nav-pills {
      margin: 0 -10px;

      .nav-item {
        padding: 0 10px 30px;
      }
      .nav-link {
        min-width: 110px;
        padding: 10px 15px;
        color: white;
        text-align: center;
        border: 1px solid white;
        border-radius: 14px;
      }
      .nav-link.active,
      .nav-pills .show>.nav-link {
        background-color: $guava;
        border-color: transparent;
      }
    }
  }
}
@media (max-width: 1199px) {
  .contato {
    &--list {
      .item {
        flex: 0 0 12%;

        &-wrap {
          padding: 20px 5px;
          font-size: 15px;
        }
      }
    }
    &-escritorios {
      background-size: auto 100%;

      .img-responsive {
        position: relative;
        width: 100%;
        height: 100%;

        img {
          position: absolute;
          top: 0;
          right: 0;
          width: auto;
          height: 100%;
          max-width: none;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .contato {
    &--list {
      flex-wrap: wrap;
      justify-content: center;

      .item {
        flex: 0 0 calc(25% - 20px);
        margin: 10px 10px;
        font-size: 15px;
      }
    }
  }
}
@media (max-width: 575px) {
  .contato {
    &--list {
      justify-content: flex-start;

      .item {
        flex: 0 0 calc(50% - 20px);
      }
    }
  }
}