.btn {
	padding: 8px 25px;
	white-space: normal;
	border: 1px solid transparent;
	border-radius: 14px;
	transition: all .3s;

	&-min {
		min-width: 120px;
	}
	&.btn-blue {
		color: white;
		background-color: $blue;

		&:hover, &:focus {
			color: white;
			background-color: darken($blue, 8%);
		}
	}
	&.btn-navy {
		color: white;
		background-color: $navy;

		&:hover, &:focus {
			color: white;
			background-color: darken($navy, 8%);
		}
	}
	&.btn-guava {
		color: white;
		background-color: $guava;

		&:hover, &:focus {
			color: white;
			background-color: darken($guava, 8%);
		}
	}
	&.btn-outline {
		color: white;
		background-color: transparent;
		border-color: white;

		&:hover, &:focus {
			color: white;
			background-color: $guava;
			border-color: transparent;
		}
	}
	&.btn-select {
		display: inline-flex;
		align-items: center;
	    width: 100%;
	    padding: .375rem .75rem;
	    color: $font-color;
	    font-size: 1rem;
	    line-height: 1.5;
	    background-color: #fff;
	    background-clip: padding-box;
	    border: 1px solid #ced4da;
	    border-radius: 2px;

		&:after {
		    content: "\e005";
				display: inline-block;
		    width: auto;
		    height: auto;
		    margin-left: 6px;
		    color: $blue;
		    font-family: $font-icons;
		    font-size: 12px;
		    border:0;
		}
		&[aria-expanded="true"] {
			border-color: $blue;

			&:after {
		    	content: "\e008";
			}
		}
	    ~ .dropdown-menu {
	    	padding: 0;
	    	margin: 0;
	    	border-color: $blue;
	    	border-radius: 2px;

			&[x-placement^="bottom"] {
				top: -1px !important;
			}
			&[x-placement^="top"] {
				top: 1px !important;
			}

	    	.inner {
	    		display: block;
	    	}
	    	li {
	    		padding: 0 5px;

	    		a {
	    			display: block;
	    			padding: 4px 10px;
	    			color: $font-color;

	    			&:focus {
	    				outline: none;
	    			}
	    		}
	    		&:hover {
	    			background-color: rgba($blue, .1);
	    		}
	    		&:not(:last-child) {
	    			a {
	    				border-bottom: 1px solid #ccc;
	    			}
	    		}
	    	}
	    }
	}
}
.btn-mais {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-self: flex-end;
	margin: auto 0 0;
	color: $blue;
	font-size: 16px;
	font-weight: bold;
	transition: all .5s;

	.icon {
		color: $guava;
		font-size: 20px;
		margin-top: 2px;
		margin-left: 5px;
		transition: all .5s;
	}
	&:hover {
		color: $navy;
		text-decoration: none;

		.icon {
			margin-left: 10px;
		}
	}
}