.modal-contato {
	color: white;

	.modal-dialog {
		@media (min-width: 768px) {
			max-width: 600px;
		}
	}
	.modal-content {
		padding: 50px 75px 30px;
		background-color: $blue;
		border-radius: 14px;
	}
	.close {
		position: absolute;
		top: 10px;
		right: 10px;
		transform: rotate(45deg);
		color: white;
		font-size: 30px;
		opacity: 1;

		&:focus {
			outline: none;
		}
	}
	.modal-title {
		margin-bottom: 20px;
		font-size: 36px;
		font-weight: 300;
	}
	p {
		font-size: 18px;
	}
	em {
		font-size: 14px;
	}
	.form {
		padding-top: 20px;

		.form-label {
			font-size: 20px;
		}
		.form-control {
			padding-top: 10px;
			padding-bottom: 8px;
			color: $navy;

			&::placeholder {
				color: $navy;
			}
			&.is-invalid {
				border: 1px solid red;

				&::placeholder {
					color: red;
				}
			}
		}
		textarea.form-control {
			margin-top: 30px;
		}
		.form-check {
			padding-left: 0;

			input,
			label {
				display: inline;
				vertical-align: middle;
			}
			a {
				color: white;
				text-decoration: underline;
			}
			small {
				display: block;
				padding: 2px 5px;
				margin-top: 5px;
				color: red;
				background-color: white;
			}
		}
		.btn {
			padding: 8px 45px;
		}
	}
}
.modal-atendimento {
	color: white;

	.modal-dialog {
		@media (min-width: 768px) {
			max-width: 600px;
		}
	}
	.modal-content {
		align-items: flex-start;
		padding: 40px 75px 40px;
		background-color: $blue;
		border-radius: 14px;
	}
	.close {
		position: absolute;
		top: 10px;
		right: 10px;
		transform: rotate(45deg);
		color: white;
		font-size: 30px;
		opacity: 1;

		&:focus {
			outline: none;
		}
	}
	.modal-title {
		margin-bottom: 20px;
		font-size: 36px;
		font-weight: 300;
		line-height: 1;
	}
	.modal-subtitle {
		color: $navy;
		margin-bottom: 0;
		font-size: 18px;
	}
	p {
		font-size: 16px;
	}
	a {
		color: white;
	}
	.nav-pills {
		margin: 10px -5px 0;

		.nav-item {
			padding: 0 6px 15px;
		}
		.nav-link {
			min-width: 100px;
			color: white;
			text-align: center;
			background-color: $navy;
			border-radius: 14px;
			transition: background-color .2s;

			&:hover,
			&.active {
				background-color: $guava;
			}
		}
		.nav-pills .show>.nav-link {
			background-color: $guava;
		}
	}
}
.modal-media {
	padding-right: 0 !important;

	.modal-dialog {
		width: calc(100% - 30px);
		max-width: 1150px;
	}
	.close {
		position: absolute;
		top: -55px;
    right: -15px;
		transform: rotate(45deg);
		display: flex;
		color: white;
		font-size: 60px;
		opacity: 1;

		&:focus {
			outline: none;
		}
	}
	.img-responsive {
		margin-bottom: 0;

		img {
			width: 100%;
			max-width: none;
		}
	}
	.embed-responsive,
	img,
	video {
		display: none;

		&.show {
			display: block;
		}
	}
}
@media (max-width: 1199px) {
	.modal-media {
		.modal-dialog {
			margin: .5rem 15px;
		}
	}
}
@media (max-width: 575px) {
	.modal-media {
		.close {
			top: -45px;
			right: -10px;
			font-size: 40px;
		}
	}
}