.breadcrumb {
  flex-wrap: nowrap;
  width: 100%;
  padding: 0;
  margin-bottom: 40px;
  background-color: transparent;

  &-wrap {
    .container {
      @media (min-width: 1300px) {
        max-width: 1300px;
      }
    }
  }
  &-item {
    display: inline-flex;
    align-items: center;
    font-size: 16px;
    line-height: 30px;

    > a {
      color: $font-color;

      &:hover {
        color: $blue;
        text-decoration: none;
      }
    }
    + .breadcrumb-item {
      padding-left: 15px;

      &:before {
        content: "\e020";
        padding-right: 15px;
        color: #ababab;
        font-family: $font-icons;
        font-size: 20px;
        line-height: 30px;
      }
    }
    &.active {
      &:before {
        color: $guava;
      }
      span {
        display: -webkit-box;
        height: 30px;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        text-overflow: ellipsis;
        color: $blue;
        overflow: hidden;
      }
    }
  }
}

@media (max-width: 767px) {
  .breadcrumb {
    flex-wrap: nowrap;

    &-wrap {
      .container {
        max-width: none;
      }
    }
    &-item {
      font-size: 14px;
      line-height: 18px;

      > a {
        &:hover {
          color: $blue;
          text-decoration: none;
        }
      }
      + .breadcrumb-item {
        padding-left: 6px;

        &:before {
          padding-right: 6px;
          font-size: 14px;
          line-height: 18px;
        }
      }
      &.active {
        span {
          height: 18px;
        }
      }
    }
  }
}