.informativos {
  padding: 60px 0;

  &--title {
    color: $navy;
    font-size: 32px;
    font-weight: 300;
    text-align: center;
  }
  &--list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 40px -15px;
  }
  &--item {
    flex: 0 0 30%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 15px;
    color: $font-color;
    // border-radius: 14px;
    transition: all .3s;

    p {
      margin-bottom: 0;
    }
    .icon {
      align-self: center;
      margin-left: 15px;
      color: $blue;
      opacity: 0;
      transition: all .3s;
    }
    &:hover {
      color: $font-color;
      text-decoration: none;
      box-shadow: 0 0 10px rgba(#282828, .1);

      .icon {
        opacity: 1;
      }
    }
  }
}
@media (max-width: 1199px) {
  .informativos {
    &--item {
      flex: 0 0 33.3333%;
    }
  }
}
@media (max-width: 991px) {
  .informativos {
    &--list {
      margin: 20px -15px;
    }
    &--item {
      flex: 0 0 100%;
      margin-bottom: 20px;
    }
  }
}