.news {
	position: relative;
	padding-top: 60px;
	overflow: visible;
	background-color: $navy;
	// background-image: url("../img/parallax-bg.jpg");
	// background-attachment: fixed;
	// background-position: center;
	// background-repeat: no-repeat;
	// background-size: cover;

	&:after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 210px;
		background-color: white;
	}
	&--wrap {
		position: relative;
	}
	.section-title {
		margin-top: 0;
		margin-bottom: 20px;
		text-align: center;
		color: $guava;
		font-weight: 300;

		a {
			color: $guava;

			&:hover {
				color: darken($guava, 10%);
				text-decoration: none;
			}
		}
	}
	&--subtitle {
		color: white;
		text-align: center;
		max-width: 500px;
		margin: 0 auto 40px;
	}
}
.owl-news.owl-carousel {
	padding: 0 15px;

	.owl-stage {
		display: flex;
	}
	.owl-item {
		padding-top: 20px;
		padding-right: 15px;
		padding-bottom: 10px;
		padding-left: 15px;
		transition: all .8s;

		&:hover {
			padding-top: 0;
			padding-bottom: 20px;
		}
	}
	.owl-nav {
		display: block;

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 8px !important;
			background-color: $blue;
			border-radius: 100%;
			transition: all .4s;

			&:hover {
				background-color: darken($blue, 10%);
			}
			&:focus {
				outline: none;
			}
			.icon {
				color: white;
				font-size: 20px;
			}
		}
		.owl-prev {
			left: -10px;

			&:hover {
				left: -15px;
			}
		}
		.owl-next {
			right: -10px;

			&:hover {
				right: -15px;
			}
		}
	}
	.item {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		height: 100%;
		color: $font-color;
		background-color: white;
		border-radius: 14px;
		box-shadow: 0 0 10px rgba(#282828, .1);
		transition: all .4s;
		overflow: hidden;

		&:hover {
			// padding: 30px 30px 60px;
			color: white;
			text-decoration: none;
			background-color: $navy;

			.news {
				&--title {
					color: $guava;
				}
				&--img {
					position: relative;

					&:after {
						content: "";
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background-color: rgba($navy, .5);
					}
					span {
						background-color: $navy;
					}
				}
				&--mais {
					color: $navy;

					&:after {
						width: 100%;
						opacity: 1;
					}
					.icon {
						margin-left: 10px;
					}
				}
			}
		}
		&.item-picture,
		&.item-video {
			.news--img {
				display: flex;
				justify-content: center;

				img {
					width: 100%;
					height: auto;
				}
			}
		}
	}
	.news {
		&--img {
			position: relative;
			flex-grow: 1;
			max-height: 221px;
			overflow: hidden;

			&:after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: transparent;
				transition: all .4s;
			}
			span {
				position: absolute;
				bottom: 0;
				left: 15px;
				z-index: 1;
				display: inline-block;
				min-width: 50%;
				max-width: calc(100% - 30px);
				padding: 5px 10px;
				color: white;
				font-size: 12px;
				background-color: $blue;
				border-top-left-radius: 2px;
				border-top-right-radius: 2px;
				transition: all .4s;
			}
		}
		&--txt {
			padding: 15px;

			p {
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 4;
				height: 80px;
				-o-text-overflow: ellipsis;
				text-overflow: ellipsis;
				overflow: hidden;
				font-size: 16px;
				line-height: 20px;
			}
		}
		&--title {
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 3;
			height: 66px;
			-o-text-overflow: ellipsis;
			text-overflow: ellipsis;
			overflow: hidden;
			color: $blue;
			font-size: 18px;
			line-height: 22px;
		}
		&--mais {
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-self: flex-end;
			margin: auto 0 0;
			color: $blue;
			font-size: 14px;
			font-weight: bold;
			transition: all .5s;

			&:after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				width: 0;
				height: 2px;
				background-color: $blue;
				opacity: 0;
				transition: all .3s;
			}
			.icon {
				color: $navy;
				font-size: 13px;
				margin-left: 5px;
				transition: all .5s;
			}
		}
	}
}
.detalhe-noticia {
	text-align: center;

	&--title {
		max-width: 1000px;
		margin-right: auto;
		margin-left: auto;
		margin-bottom: 40px;
		color: $blue;
		font-size: 36px;
		font-weight: 300;
	}
	&--date {
		display: block;
		margin-bottom: 15px;
		font-size: 16px;
		font-weight: bold;
	}
	&--tag {
		padding: 5px 10px;
		color: white;
		font-size: 16px;
		font-weight: bold;
		line-height: 1;
		background-color: $blue;
	}
	&--txt {
		padding-top: 50px;
		text-align: left;

		h3 {
			margin-top: 25px;
			color: $blue;
			font-size: 20px;
		}
		.img {
			float: right;
			max-width: calc(50% - 15px);
			// padding-left: 15px;
			margin-left: 30px;
			border-radius: 14px;
			overflow: hidden;

			img {
				max-width: 100%;
			}
		}
	}
	&--bg {
		margin-top: -3vw;

		@media (min-width: 1800px) {
			margin-top: -7vw;
		}
		img {
			width: 100%;
		}
	}
}
.noticias-recentes {
	padding-top: 30px;

	&--title {
		color: $navy;
		font-size: 36px;
		font-weight: 300;
		text-align: center;
	}
	.news {
		padding-top: 30px;
		padding-bottom: 40px;
		background-color: white;

		&:after {
			height: 250px;
			background-color: $navy;
		}
	}
	.owl-news.owl-carousel {
		padding: 0;

		.owl-nav {
			display: none;
		}
	}
}
.listagem-noticia {
	margin-bottom: 60px;

	.btn {
		padding: 12px 25px;
	}
	&--header {
		padding-bottom: 70px;
		margin-bottom: 50px;
		text-align: center;
		background-image: url("../img/bg-noticias.png");
		background-size: 100% auto;
		background-position: bottom left;
		background-repeat: no-repeat;

		@media (max-width: 767px) {
			background-image: none;
		}
		.icon {
			margin-top: 5px;
			margin-bottom: 20px;
			color: $blue;
			font-size: 70px;
		}
	}
	&--title {
		margin-top: 0;
		margin-bottom: 30px;
		color: $blue;
		font-size: 50px;
		font-weight: 300;
	}
	&--subtitle {
		max-width: 700px;
		margin: 0 auto;
		color: $blue;
	}
	&--filtro {
		display: flex;
		justify-content: space-between;
		max-width: 700px;
		margin: 0 auto 20px;

		.filtro--item {
			flex: 0 0 110px;

			input {
				position: absolute;
				top: 0;
				left: 0;
				visibility: hidden;

				&:checked {
					~ label {
						color: white;
						background-color: $blue;
					}
				}
			}
			label {
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 12px 10px;
				color: $blue;
				border: 3px solid $blue;
				border-radius: 14px;
				cursor: pointer;
				transition: all .3s;
			}
		}
	}
	&--list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin: 0 -15px;
	}
	&--item {
		flex: 0 0 50%;
		max-width: 550px;
		padding: 35px 15px;
	}
	.item {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		height: 100%;
		color: $font-color;
		background-color: white;
		border-radius: 14px;
		box-shadow: 0 0 10px rgba(#282828, .1);
		transition: all .4s;
		overflow: hidden;

		&:hover {
			// padding: 30px 30px 60px;
			color: white;
			text-decoration: none;
			background-color: $navy;

			.news {
				&--img {
					position: relative;

					&:after {
						background-color: rgba($navy, .5);
					}
					span {
						background-color: $navy;
					}
				}
				&--title {
					color: $guava;
				}
				&--mais {
					color: white;

					&:after {
						width: 100%;
						opacity: 1;
					}
					.icon {
						margin-left: 10px;
					}
				}
			}
		}
		&.item-picture,
		&.item-video {
			.news {
				&--img {
					flex-grow: 1;

					img {
						width: 100%;
						height: auto;
					}
				}
				&--title {
					-webkit-line-clamp: 2;
    			height: 44px;
				}
			}
		}
		&.item-video {
			.news {
				&--img {
					&:before {
						content: "";
						position: absolute;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						width: 0;
						height: 0;
						border-style: solid;
						border-width: 25px 0 25px 50px;
						border-color: transparent transparent transparent #ffffff;
					}
				}
			}
		}
	}
	.news {
		&--img {
			position: relative;
			display: flex;
			justify-content: center;
			overflow: hidden;

			&:after {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: transparent;
				transition: all .4s;
			}
			img {
				width: 100%;
			}
			span {
				position: absolute;
				bottom: 0;
				left: 15px;
				z-index: 1;
				display: inline-block;
				min-width: 50%;
				max-width: calc(100% - 30px);
				padding: 5px 10px;
				color: white;
				font-size: 12px;
				background-color: $blue;
				border-top-left-radius: 2px;
				border-top-right-radius: 2px;
				transition: all .4s;
			}
		}
		&--txt {
			flex-grow: 1;
			display: flex;
			flex-direction: column;
			padding: 15px;

			p {
				display: -webkit-box;
				-webkit-box-orient: vertical;
				-webkit-line-clamp: 4;
				max-height: 84px;
				-o-text-overflow: ellipsis;
				text-overflow: ellipsis;
				overflow: hidden;
				font-size: 16px;
				line-height: 21px;
			}
		}
		&--title {
			display: -webkit-box;
			-webkit-box-orient: vertical;
			-webkit-line-clamp: 3;
			height: 66px;
			-o-text-overflow: ellipsis;
			text-overflow: ellipsis;
			overflow: hidden;
			color: $navy;
			font-size: 18px;
			line-height: 22px;
		}
		&--mais {
			position: relative;
			display: inline-flex;
			align-items: center;
			justify-self: flex-end;
			margin: auto 0 0;
			color: $blue;
			font-size: 16px;
			font-weight: bold;
			transition: all .5s;

			.icon {
				color: $guava;
				font-size: 20px;
				margin-top: 2px;
				margin-left: 5px;
				transition: all .5s;
			}
		}
	}
}

@media (min-width: 1200px) {
	.owl-news.owl-carousel {
		.owl-nav {
			.owl-prev {
				left: -30px;

				&:hover {
					left: -35px;
				}
			}
			.owl-next {
				right: -30px;

				&:hover {
					right: -35px;
				}
			}
		}
	}
	.listagem-noticia {
		.news {
			&--img {
				max-height: 350px;
			}
		}
	}
}
@media (min-width: 1300px) {
	.owl-news.owl-carousel {
		.owl-nav {
			.owl-prev {
				left: -90px;

				&:hover {
					left: -95px;
				}
			}
			.owl-next {
				right: -90px;

				&:hover {
					right: -95px;
				}
			}
		}
	}
}
@media (max-width: 1199px) {
	.owl-news.owl-carousel {
		.news {
			&--img {
				max-height: 181px;
			}
		}
	}
	.listagem-noticia {
		&--list {
			margin-bottom: 40px;
		}
		&--item {
			flex: 0 0 50%;
			max-width: 50%;
			padding: 15px;
		}
		.news {
			&--img {
				max-height: 300px;
			}
		}
	}
}
@media (max-width: 991px) {
	.owl-news.owl-carousel {
		.news {
			&--img {
				max-height: 200px;
			}
		}
	}
	.listagem-noticia {
		.news {
			&--img {
				max-height: 222px;
			}
		}
	}
}
@media (max-width: 767px) {
	.owl-news.owl-carousel {
		.item {
			&.item-picture,
			&.item-video {
				.news--img {
					flex-grow: unset;

					img {
						width: 100%;
						height: auto;
					}
				}
			}
		}
		.news {
			&--img {
				max-height: none;
			}
		}
	}
	.listagem-noticia {
		&--filtro {
			flex-wrap: wrap;
			justify-content: center;

			.filtro--item {
				flex: 0 0 30%;
				padding: 0 5px;
			}
		}
		.news {
			&--img {
				max-height: 160px;
			}
		}
	}
}
@media (max-width: 575px) {
	.detalhe-noticia {
		text-align: center;

		&--title {
			margin-bottom: 30px;
			font-size: 26px;
		}
		&--txt {
			padding-top: 30px;

			.img {
				float: none;
				max-width: none;
				padding-left: 0;
				margin-left: 0;

				img {
					max-width: 100%;
				}
			}
		}
		&--bg {
			margin-top: 0;
		}
	}
	.listagem-noticia {
		&--header {
			background-image: none;
			padding-bottom: 0;
		}
		&--filtro {
			flex-wrap: wrap;
			justify-content: center;

			.filtro--item {
				flex: 0 0 50%;
				padding: 0 5px;
			}
		}
		&--item {
			flex: 0 0 100%;
			max-width: 100%;

			&.item-picture,
			&.item-video {
				.news {
					&--img {
						max-height: none;

						img {
							width: 100%;
							height: auto;
						}
					}
				}
			}
			.news {
				&--img {
					max-height: none;
				}
			}
		}
	}
}