.navios {
  padding: 60px 0 80px;
  background-color: #e9eaeb;

  &--title {
    margin-bottom: 30px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
}

.table-navios {
  margin-top: 50px;
  margin-bottom: 0;
  background-color: white;
  border-radius: 14px;
  overflow: hidden;

  @media (min-width: 1200px) {
    font-size: 18px;
  }
  >thead {
    th {
      padding-top: 20px;
      padding-bottom: 10px;
      color: $blue;
      font-size: 22px;
      font-weight: normal;
      text-transform: uppercase;
      border: none;
    }
  }
  >tbody {
    tr {
      color: black;

      .icon {
        color: $blue;
        font-size: 23px;
      }
    }
  }
  [data-toggle="collapse"] {
    &:hover,
    &[aria-expanded="true"] {
      color: white;
      background-color: $blue;
      cursor: pointer;

      .icon {
        color: white;
      }
    }
    &[aria-expanded="true"] {
      .icon {
        transform: rotate(180deg)
      }
    }
    + tr {
      td, th {
        color: white;
        font-weight: normal;
        border: none;
      }
      > td,
      th:last-child {
        padding: 0;

        img {
          width: 100%;
        }
      }
      th:first-child {
        vertical-align: middle;
        background-color: $guava;

        @media (min-width: 992px) {
          padding: 15px 45px;
        }
      }
      tbody {
        tr {
          &:nth-child(odd) {
            background-color: #1e8ace;
          }
          &:nth-child(even) {
            background-color: $blue;
          }
        }
      }
    }
  }
  .table {
    margin-bottom: 0;
  }
}