.balsa {
  min-height: 300px;
  color: white;
  background-image: url("../img/bg-water.jpg");
  background-size: 100% auto;
  overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 15px;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
      }
    }
  }

  &--beneficios {
    padding-top: 60px;
    padding-bottom: 30px;
    background-image: url("../img/bg-category.png");
    background-size: 100% auto;
    background-position: bottom left;
    background-repeat: no-repeat;

    @media (max-width: 767px) {
			background-image: none;
		}

    .container {
      @media (min-width: 1300px) {
        max-width: 1200px;
      }
    }
    .beneficios {
      &--title {
        margin-bottom: 30px;
        color: $navy;
        font-size: 36px;
        font-weight: 300;
        line-height: 1;
      }
      &--list {
        padding-left: 5px;
        margin-bottom: 0;
        list-style: none;

        li {
          margin-bottom: 15px;

          &:before {
            content: "";
            display: inline-block;
            width: 11px;
            height: 11px;
            margin-right: 15px;
            background-color: #467bc3;
            border-radius: 100%;
          }
        }
      }
    }
    .icon-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      padding-left: 0;
      margin-bottom: 25px;
      list-style: none;

      li {
        flex: 0 0 16.6666%;
        padding: 15px;
        text-align: center;

        img {
          height: 72px;
          margin-bottom: 10px;
        }
        p {
          margin-bottom: 0;
          white-space: nowrap;
        }
        .color {
          color: #467bc3;
          font-size: 18px;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .balsa {
    background-size: auto 100%;

    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
  }
}