.diferenciais {
  padding: 60px 0 40px;

  &--title {
    margin-bottom: 30px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -15px;
  }
  &--item {
    flex: 0 0 33.3333%;
    padding: 25px;
    text-align: center;

    img {
      max-height: 78px;
      margin-bottom: 10px;
    }
    h3 {
      min-height: 64px;
      color: $navy;
      font-size: 30px;
      font-weight: 300;
      line-height: 32px;
    }
  }
}

@media (max-width: 1199px) {
  .diferenciais {
    padding: 60px 0;

    &--item {
      padding: 15px;
    }
  }
}
@media (max-width: 991px) {
  .diferenciais {
    &--item {
      img {
        max-height: 70px;
      }
      h3 {
        min-height: 60px;
        font-size: 28px;
        line-height: 30px;
      }
      p {
        font-size: 15px;
      }
    }
  }
}
@media (max-width: 767px) {
  .diferenciais {
    &--item {
      flex: 0 0 50%;
    }
  }
}
@media (max-width: 575px) {
  .diferenciais {
    &--item {
      flex: 0 0 100%;
    }
  }
}