.video {
  padding-top: 60px;

  &--title {
    margin-bottom: 30px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--item {
    position: relative;
    overflow: hidden;
  }
  &--thumb {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    cursor: pointer;

    &:after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 3vw 0 3vw 6vw;
      border-color: transparent transparent transparent #ffffff;
    }
    img {
      width: 100%;
      height: 100%;
    }
    &.hide {
      display: none;
    }
  }
}

@media (max-width: 767px) {
  .video {
    &--title {
      font-size: 30px;
    }
  }
}