.rotas {
  padding: 60px 0;

  .second-row {
    align-items: flex-end;
    margin-top: -100px;
  }
  &--title {
    margin-bottom: 40px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--form {
    margin-bottom: 20px;

    .form-title {
      margin-top: 30px;
      margin-bottom: 20px;
      color: $blue;
      font-size: 20px;
    }
    select.form-control {
      height: 32px;
      border-radius: 999px;
    }
  }
  &--mapa {
    margin-top: 60px;
  }
  &--legenda {
    margin-bottom: 0;
    text-align: right;
  }

  .rota {
    &-title {
      padding-bottom: 4px;
      margin-bottom: 30px;
      color: $blue;
      font-size: 22px;
      border-bottom: 2px solid $medium-gray;
    }
    &-subtitle {
      margin-bottom: 20px;
      font-size: 20px;
    }
    &-box {
      padding: 10px 20px;
      margin-bottom: 30px;
      background-color: white;
      border-radius: 14px;
      box-shadow: 0 0 10px rgba(#282828, .1);

      .row {
        margin-right: -5px;
        margin-left: -5px;

        [class*="col-"] {
          padding: 5px;

          &:first-child {
            color: $blue;
          }
        }
      }
      small {
        display: block;
        line-height: 1;
      }
    }
  }
}

@media (min-width: 1000px) {
  .rotas {
    >.container {
      max-width: 1000px;
    }
    .first-row {
      min-height: 870px;
      padding-bottom: 60px;
      background-image: url("../img/mapa-bg.jpg");
      background-size: 700px auto;
      background-position: 82% 0;
      background-repeat: no-repeat;
    }
    &--form {
      select.form-control {
        height: 32px !important;
        padding-top: 0;
        padding-bottom: 0;
        line-height: 1;
        border-radius: 999px;
      }
    }
    &--btns {
      .btn {
        padding: 12px 15px;
        min-width: 190px;

        + .btn {
          margin-left: 10px;
        }
      }
    }
  }
}
@media (max-width: 999px) {
  .rotas {
    >.container {
      max-width: none;
    }
    .second-row {
      flex-direction: column-reverse;
    }
    &--mapa {
      margin-top: 0;
      // text-align: center;
    }
    &--btns {
      margin-top: 20px;
    }
    .rota {
      >.row {
        [class*="col-"] {
          display: flex;
          flex-direction: column;
        }
      }
      &-box {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }
  }
}
@media (max-width: 575px) {
  .rotas {
    .second-row {
      flex-direction: row;
      align-items: flex-start;
      margin-top: 0;
    }
    &--btns {
      margin-top: 0;

      .btn {
        width: 100%;
        min-width: none;
        padding: 8px 10px;
        font-size: 14px;

        + .btn {
          margin-top: 10px;
          margin-left: 0;
        }
      }
    }
  }
}