.login {
  padding: 50px 0;
  background-color: $blue;
  background-image: url("../img/bg-login.png");
  background-size: 100% auto;
  background-position: center;
  background-repeat: no-repeat;

  &--box {
    display: flex;

    .descubra,
    .portal {
      flex: 0 0 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 25px 15px;
      background-color: white;
      border-radius: 14px 0 0 14px;

      &--wrap {
        width: 435px;
        max-width: 100%;
      }
      &--title {
        margin-bottom: 20px;
        font-size: 32px;
        font-weight: 300;
        line-height: 1.1;

        strong {
          color: $blue;
          font-weight: 300;
        }
      }
      &--list {
        padding-left: 0;
        margin-bottom: 25px;
        list-style: none;

        li {
          margin-bottom: 5px;
          color: $blue;
          font-weight: normal;
          // line-height: 1;

          &:before {
            content: "\e007";
            margin-right: 5px;
            font-family: $font-icons;
            font-size: 14px;
            line-height: 16px;
          }
        }
      }
      p {
        color: $font-color;
        font-size: 18px;

        a {
          color: $blue;
        }
      }
    }
    .portal {
      color: white;
      background-color: $navy;
      border-radius: 0 14px 14px 0;

      &--title {
        color: white;

        strong {
          color: $guava;
        }
      }
      &--list {
        li {
          color: white;

          &:before {
            color: $guava;
          }
          strong {
            margin-right: 4px;
            color: $guava;
            font-weight: normal;
          }
        }
      }
      p {
        color: white;

        a {
          color: white;
        }
      }
    }
  }
  p {
    margin-top: 20px;
    margin-bottom: 0;
    color: white;
    font-size: 20px;

    a {
      color: $navy;
      // font-weight: bold;
      text-decoration: underline;
    }
  }
}

@media (min-width: 1500px) {
  .login {
    >.container {
      max-width: 1400px;
    }
    &--box {
      .descubra,
      .portal {
        &--wrap {
          width: 500px;
        }
        &--title {
          font-size: 36px;
        }
      }
    }
  }
}
@media (max-width: 1199px) {
  .login {
    &--box {
      .descubra,
      .portal {
        &--wrap {
          width: 405px;
        }
        &--title {
          font-size: 30px;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .login {
    &--box {
      flex-wrap: wrap;

      .descubra,
      .portal {
        flex: 0 0 100%;

        &--wrap {
          width: 100%;
          max-width: 405px;
        }
      }
      .descubra {
        border-radius: 14px 14px 0 0;
      }
      .portal {
        border-radius: 0 0 14px 14px;
      }
    }
  }
}
@media (max-width: 575px) {
  .login {
    &--box {
      .descubra,
      .portal {
        &--title {
          font-size: 28px;
        }
        p {
          font-size: 16px;
        }
      }
    }
    p {
      font-size: 18px;
    }
  }
}