.footer {
	&--info {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 20px 3vw 0;
	}
	&--logo {
		flex: 0 0 195px;
		margin-bottom: 0;

		img {
			max-width: 100%;
		}
	}
	&--social {
		p {
			color: #545454;
			font-size: 16px;
			margin-bottom: 0;
		}
		.social-list {
			display: flex;
			justify-content: center;
			padding-left: 0;
			margin-bottom: 0;
			list-style: none;

			li {
				padding: 6px;

				a {
					display: flex;
					align-items: center;
					justify-content: center;
					padding: 10px;
					color: white;
					font-size: 25px;
					background-color: $blue;
					border-radius: 100%;
					transition: all .3s;

					&:hover {
						text-decoration: none;
						background-color: $navy;
					}
				}
			}
		}
	}
	&--contato {
		text-align: right;

		&:hover {
			text-decoration: none;
		}
		figure {
			margin-bottom: 0;
		}
		p {
			margin-bottom: 0;
			color: $font-color;
			font-size: 16px;
		}
		.contato--title {
			margin: 0;
			color: $navy;
			font-size: 24px;
			font-weight: bold;
		}
	}
	&--copyright {
		display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 0 15px 30px;
	}
	&--list {
		display: flex;
		align-items: center;
		justify-content: center;
		list-style: none;
		padding: 15px 0;
		margin: 0 -10px;

		li {
			padding: 0 10px;
			color: #545454;
			font-size: 15px;
			line-height: .9;
			text-align: center;

			&:not(:last-child) {
				border-right: 1px solid #545454;
			}
			a {
				display: inline-block;
				color: #545454;
				line-height: .9;
			}
		}
	}
}
@media (max-width: 767px) {
	.footer {
		&--info {
			flex-direction: column;
		}
		&--logo {
			flex: 0 0 auto;
			width: 195px;
			margin-bottom: 15px;
		}
		&--social {
			margin-bottom: 15px;
		}
		&--contato {
			text-align: center;

			figure {
				margin-bottom: 0;

				img {
					width: 118px;
				}
			}
			.contato--title {
				font-size: 21px;
			}
		}
		&--copyright {
			padding: 0 15px 15px;
		}
		&--list {
			flex-wrap: wrap;

			li {
				flex: 0 0 100%;
				padding: 10px;
			}
		}
  }
}