.numeros {
  padding: 80px 0 50px;

  &--title {
    margin-bottom: 50px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--list {
    display: flex;
    margin: 0 -15px;
  }
  &--item {
    flex: 0 0 25%;
    padding: 15px;
    color: $navy;
    font-size: 24px;
    text-align: center;

    strong {
      color: $guava;
      font-weight: normal;
    }
    .item {
      &-img {
        height: 65px;

        img {
          max-height: 100%;
        }
      }
    }
  }
}

@media (max-width: 1199px) {
  .numeros {
    &--item {
      font-size: 22px;
    }
  }
}
@media (max-width: 991px) {
  .numeros {
    >.container {
      max-width: none;
    }
    &--list {
      margin: 0 -10px;
    }
    &--item {
      padding: 10px;
      font-size: 18px;

      .item {
        &-img {
          height: 55px;
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .numeros {
    padding: 40px 0 30px;

    &--list {
      justify-content: center;
      flex-wrap: wrap;
    }
    &--item {
      flex: 0 0 50%;
    }
  }
}