.valores {
  padding: 60px 0;
  background-image: url("../img/bg-category.png");
  background-size: 100% auto;
  background-position: bottom center;
  background-repeat: no-repeat;

  @media (max-width: 767px) {
    background-image: none;
  }

  &--title {
    margin-bottom: 30px;
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -15px;
  }
  &--item {
    flex: 0 0 33.3333%;
    display: flex;
    justify-content: center;
    padding: 15px;
    text-align: center;

    .item {
      &-img {
        img {
          height: 100%;
        }
      }
      &-wrap {
        max-width: 290px;
      }
      &-title {
        margin-bottom: 10px;
        color: $navy;
        font-size: 29px;
        font-weight: 300;
      }
    }
  }
}

@media (max-width: 991px) {
  .valores {
    &--item {
      .item {
        &-img {
          height: 50px;
        }
        &-title {
          font-size: 25px;
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .valores {
    &--item {
      flex: 0 0 50%;

      .item {
        &-img {
          height: 50px;
        }
        &-title {
          font-size: 25px;
        }
      }
    }
  }
}
@media (max-width: 575px) {
  .valores {
    padding: 40px 0;

    &--item {
      flex: 0 0 100%;
    }
  }
}