.herotitle {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 350px;
  padding: 5vw 0;
  color: white;
  text-align: center;
  background-color: $navy;

  .container {
    max-width: 600px;
  }
  &--title {
    margin-top: 0;
    margin-bottom: 30px;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
    line-height: 1.3em;

    strong {
      color: $guava;
      font-weight: 300;
    }
  }
  a {
    color: $guava;
  }
  .btn {
    padding: 10px 25px;

    +.btn {
      margin-left: 10px;
    }
  }
  #collapseMar {
    a {
      display: block;
      padding: 15px 0;
      color: white;
    }
  }

  &.contato {
    .container {
      max-width: 1140px;
    }
  }
}
@media (max-width: 575px) {
  .herotitle {
    .container {
      max-width: 350px;
    }
    &--title {
      font-size: 28px;
    }
  }
}