.owl-principal {
	position: relative;

	.owl-stage {
		display: flex;
		max-height: 100vh;
		overflow: hidden;
	}
	.owl-carousel {
		display: block;

		.item {
			height: 100%;

			&-txt {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: 35%;
				color: white;
				text-align: center;

				p {
					font-size: 1.6vw;
					margin-bottom: 0;
				}
			}
			&-title {
				margin-top: 0;
				font-size: 3.4vw;
			}
		}
		.owl-nav {
			.owl-prev,
			.owl-next {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				color: white;
				font-size: 4vw;

				&:focus {
					outline: none;
				}
			}
			.owl-prev {
				left: 0;
			}
			.owl-next {
				right: 0;
			}
		}
		.owl-dots {
			position: absolute;
			bottom: 40px;
			left: 50%;
			transform: translateX(-50%);
			z-index: 11;
			display: inline-flex;
			justify-content: center;
			overflow: hidden;
			// counter-reset: my-awesome-counter;

			.owl-dot {
				position: relative;
				width: 16px;
				height: 16px;
				border: 2px solid white;
				background-color: transparent;
				border-radius: 100%;
				transition: all .3s;

				&:not(:last-child) {
					margin-right: 50px;

					&:before {
						content: "";
						position: absolute;
						top: 50%;
						left: 100%;
						transform: translate(2px, -50%);
						width: 50px;
						height: 2px;
						background-color: white;
					}

					&.active {
						span {
							&:after {
								content: "";
								position: absolute;
								top: 50%;
								left: 100%;
								transform: translate(2px, -50%);
								width: 50px;
								height: 2px;
								background-color: $guava;
								-webkit-animation: dotActiveOut linear 8s;
								-moz-animation: dotActiveOut linear 8s;
								-o-animation: dotActiveOut linear 8s;
							}
						}
					}
				}
				&:not(:first-child) {
					&.active {
						span {
							&:before {
								content: "";
								position: absolute;
								top: 50%;
								right: 100%;
								transform: translate(-2px, -50%);
								width: 50px;
								height: 2px;
								background-color: $guava;
								-webkit-animation: dotActiveIn linear 8s;
								-moz-animation: dotActiveIn linear 8s;
								-o-animation: dotActiveIn linear 8s;
							}
						}
					}
				}
				&.active {
					background-color: $guava;
					border-color: $guava;
				}
			}
		}
	}
}

@-webkit-keyframes dotActiveIn {
	0% 		{width: 50px;}
	100%  {width: 0;}
}
@-webkit-keyframes dotActiveOut {
	0% 		{width: 0;}
	100%  {width: 50px;}
}
@-webkit-keyframes dotActiveInSm {
	0% 		{width: 30px;}
	100%  {width: 0;}
}
@-webkit-keyframes dotActiveOutSm {
	0% 		{width: 0;}
	100%  {width: 30px;}
}

@media (max-width: 991px) {
	.owl-principal {
		.owl-carousel {
			.item {
				&-txt {
					width: 450px;

					p {
						font-size: 20px;
					}
				}
				&-title {
					font-size: 40px;
				}
			}
			.owl-dots {
				bottom: 10px;

				.owl-dot {
					width: 14px;
					height: 14px;

					&:not(:last-child) {
						margin-right: 30px;

						&:before {
							width: 30px;
						}

						&.active {
							span {
								&:after {
									width: 30px;
									-webkit-animation: dotActiveOutSm linear 8s;
									-moz-animation: dotActiveOutSm linear 8s;
									-o-animation: dotActiveOutSm linear 8s;
								}
							}
						}
					}
					&:not(:first-child) {
						&.active {
							span {
								&:before {
									width: 30px;
									-webkit-animation: dotActiveInSm linear 8s;
									-moz-animation: dotActiveInSm linear 8s;
									-o-animation: dotActiveInSm linear 8s;
								}
							}
						}
					}
				}
			}
		}
	}
}
@media (max-width: 575px) {
	.owl-principal {
		.owl-carousel {
			.item {
				&-txt {
					width: 100%;
					padding: 15px;

					p {
						font-size: 15px;
					}
				}
				&-title {
					font-size: 28px;
				}
			}
		}
	}
}