.header {
	position: fixed;
	top: -80px;
	left: 0;
	z-index: 1039;
	display: flex;
	align-items: center;
	justify-content: space-around;
	width: 100%;
	padding: 0 3vw;
	background-color: transparent;
	// border-bottom: 1px solid rgba(white, .2);
	transition: top .4s, background-color .6s ;

	&:hover {
		background-color: rgba($navy, .8);
	}
	&.show {
		top: 0;
	}
	&.bg {
		background-color: rgba($navy, .8);
	}
	&--logo {
		width: 161px;
		min-width: 149px;
		align-self: center;

		img {
			max-width: 100%;
		}
	}
	&--menu {
		flex-grow: 1;
		display: flex;
		align-items: center;
		height: 80px;

		.menu {
			flex-grow: 1;
			display: flex;
			justify-content: center;
			height: 100%;
			padding: 0 10px;
			margin: 0;
			list-style: none;

			&-item {
				padding: 0;
			}
			&-link {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				height: 100%;
				padding: 10px 8px;
				color: white;
				line-height: 1.1;
				text-align: center;
				white-space: nowrap;
				transition: background-color .3s;

				&:hover,
				&:focus {
					text-decoration: none;
					background-color: #0c6db4;
					outline: none;
				}
			}
		}
		.dropdown {
			&-menu {
				top: 100% !important;
				left: 50% !important;
    		transform: translate(-40%, -12px) !important;
				margin-top: 0;
				background-color: $blue;
				border: none;
				border-radius: 14px;
				overflow-x: hidden;
			}
			&-item {
				padding: 6px 25px;
				color: white;

				&:hover {
					background-color: #0c6db4;
				}
			}
			&.show {
				.menu-link {
					background-color: #0c6db4;
				}
			}
		}
	}
	&--search {
		position: relative;
		min-width: 110px;
		max-width: 170px;
		margin-right: 15px;

		input {
			width: 100%;
			height: 36px;
			padding: 5px 40px 7px 20px;
			color: white;
			font-size: 15px;
			background-color: rgba(black, .32);
			border: 2px solid transparent;
			border-radius: 999px;
			transition: border-color .3s;

			&::placeholder {
				color: white;
			}
			&:focus {
				border-color: white;
				outline: none;
			}
		}
		.icon {
			position: absolute;
			top: 1px;
			right: 1px;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 34px;
			height: 34px;
			color: white;
			font-size: 15px;
			background-color: transparent;
			border-radius: 100%;
			transition: all .3s;

			&:hover {
				color: $gray;
				text-decoration: none;
				background-color: white;
			}
		}
	}
	.btn-login {
		align-self: center;
		display: inline-flex;
		align-items: center;
		padding: 8px 25px;
		// margin: 0 10px;
		color: white;
		font-size: 15px;
		white-space: nowrap;
		background-color: $blue;
		border-radius: 14px;
		transition: background-color .4s;

		&:hover {
			background-color: darken($blue, 5%);

			.icon {
				margin-right: 0;
				margin-left: 15px;
			}
		}
		.icon {
			margin-right: 5px;
			margin-left: 10px;
			transition: all .3s;
		}
	}
	&--lang {
		// display: flex;
		margin-left: 10px;

		.btn {
			display: inline-flex;
			align-items: flex-end;
			padding: 5px 12px 10px 15px;
			color: white;
			white-space: nowrap;
			border: none;
			transition: none;

			&:after {
				content: "\e005";
				display: inline-block;
				width: auto;
				height: auto;
				margin-left: 10px;
				color: white;
				font-family: $font-icons;
				font-size: 12px;
				border: 0;
			}
			&:hover, &:focus, &:active {
				text-decoration: none;
			}
			&:hover {
				background-color: $navy;
			}
			&[aria-expanded="true"] {
				background-color: $navy;
				border-bottom-left-radius: 0;
				border-bottom-right-radius: 0;

				&:after {
			    content: "\e008";
				}
			}
		}
		.dropdown-menu {
			left: auto !important;
			top: 100% !important;
			right: 0;
			transform: none !important;
			width: 100%;
			min-width: 0;
			padding: 0 0 8px;
			margin: 0;
			background-color: $navy;
			border: 0;
			border-radius: 0 0 14px 14px;
			overflow: hidden;
		}
		.dropdown-item {
			display: flex;
			align-items: center;
			padding: 0 15px 3px;
			color: white;
			border: 0;
			transition: background-color, .3s;

			&:hover {
				background-color: darken($blue, 5%);
			}
		}
	}
	&--menu-toggle {
		display: none;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 5px 0 5px 10px;

		.bar {
			position: relative;
			z-index: 999;
			display: block;
			width: 30px;
			height: 4px;
			margin: 2px 0 3px;
			background-color: white;
			transition: all .5s;
		}
		&.open {
			&:before {
				content: "";
				position: fixed;
				top: 0;
				left: 0;
				z-index: 9;
				width: 100%;
				height: 100%;
				background: rgba(black, .9);
			}
			.bar {
				&:nth-child(1) {
					transform: rotate(45deg)translate(7px, 6px);
				}
				&:nth-child(2) {
					width: 0;
				}
				&:nth-child(3) {
					transform: rotate(-45deg)translate(6px, -6px);
				}
			}
		}
	}
}

@media (min-width: 1200px) {
	.header {
		padding-right: calc(3vw - 12px);
	}
}
@media (min-width: 1200px) and (max-width: 1300px) {
	.header {
		&--logo {
			min-width: 135px;
		}
		.btn-login {
			padding: 8px 15px;
		}
	}
}
@media (max-width: 1199px) {
	.header {
		justify-content: space-between;
		padding: 15px;

		&--logo {
			margin-right: auto;
		}
		&--menu {
			position: fixed;
			top: 0;
			left: -350px;
			z-index: 10;
			flex-direction: column;
			align-items: flex-start;
			justify-content: flex-start;
			width: 350px;
			height: 100%;
			padding: 20px 30px 30px;
			list-style: none;
			background-color: $navy;
			transition: left .4s;

			&.open {
				left: 0;
			}
			.menu {
				order: 2;
				justify-content: flex-start;
				flex-direction: column;
				width: 100%;
				padding: 10px 0;
				overflow: auto;

				&-item {
					&:first-child {
						.menu-link {
							border-top: 1px solid white;
						}
					}
				}
				&-link {
					display: block;
					height: auto;
					padding: 20px 15px;
					color: white;
					font-weight: bold;
					text-align: left;
					text-transform: uppercase;
					border-bottom: 1px solid white;
					transition: all .2s;

					&:hover {
						text-decoration: none;
					}
					&:focus {
						background-color: transparent;
					}
				}
				.dropdown {
					&-menu {
						position: static;
						top: 0 !important;
						left: 0 !important;
						transform: none !important;
						float: none;
						padding: 0;
						margin-top: 0;
						background-color: darken(#021859, 5%);
						border-radius: 0;
					}
					&-item {
						padding: 10px 15px;
						border-bottom: 1px solid white;
					}
					&.show {
						.menu-link {
							background-color: #0c6db4;
						}
					}
				}
			}
		}
		&--search {
			order: 1;
			width: 100%;
			max-width: none;
			margin: 0 0 30px;

			input {
				width: 100%;
				height: 42px;
			}
			.icon {
				width: 40px;
    		height: 40px;
				font-size: 20px;
			}
		}
		&--menu-toggle {
			display: flex;
		}
	}
}
@media (max-width: 767px) {
	.header {
		>.header--search {
			display: none;
		}
		&--menu {
			padding-top: 20px;
			padding-right: 15px;
			padding-left: 15px;

			.header--search {
				display: block;
				max-width: none;
				margin-right: 0;
				margin-bottom: 15px;
			}
		}
		.btn-login {
			padding: 6px 10px;
		}
		&--menu-toggle {
			padding-right: 15px;
		}
	}
}
@media (max-width: 575px) {
	.header {
		align-items: flex-start;

		&--menu-toggle {
			order: 2;
			padding-right: 0;
		}
		&--menu {
			left: -290px;
			width: 290px;
		}
		>.header--search,
		>.btn-login {
			display: none;
		}
	}
}