.input-qtd {
	position: relative;
	width: 100px;

	.form-control {
		padding: 8px 23px;
    color: $blue;
		line-height: 1;
		text-align: center;
		// border-width: 0;
    border-radius: 12px;
		-moz-appearance: textfield;

		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
		}
    &:focus {
      box-shadow: none;
    }
		&:disabled {
			color: #bdbdbd;

			~ .input-qtd {
				&--increase,
				&--decrease {
					display: none;
				}
			}
		}
	}
	&--increase,
	&--decrease {
    position: absolute;
    top: 0;
    display: inline-flex;
    align-items: center;
		justify-content: center;
		// width: 18px;
    height: 100%;
		padding: 5px 3px;
		color: $blue;
		font-size: 15px;

		&:hover {
			color: white;
			text-decoration: none;
			background-color: $blue;
		}
	}
  &--increase {
    right: 0;
    border-radius: 0 12px 12px 0;

    .icon {
      transform: rotate(90deg);
    }
  }
	&--decrease {
    left: 0;
    border-radius: 12px 0 0 12px;
  }
}