.landing {
  color: #515151;
  font-family: 'Maersk Text';

  h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    @media (min-width: 1200px) {
      margin-bottom: 1.2rem;
    }
  }
  .btn {
    padding: 15px 45px;
    font-size: 16px;
    font-weight: bold;
    text-transform: none;
    border-radius: 6px;
  }
  .header {
    top: -100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 25px 30px 25px;
    background-color: transparent;
    border: none;

    &.bg {
      background-color: rgba(2, 24, 89, 0.56);
    }
    &.show {
      top: 0;
    }
    &__logo {
      width: 150px;
      margin-bottom: 0;

      img {
        max-width: 100%;
      }
    }
    &__nav {
      margin: 0 -10px 15px;

      .nav-link {
        padding: 5px 10px;
        color: white;
        font-size: 15px;
        border-radius: 6px;
        transition: all .3s;

        &:hover {
          background-color: $guava;
        }
      }
    }
    .nav-toggle {
      display: none;
    }
  }
  .banner {
    position: relative;
    text-align: center;

    &__video {
      width: 100%;
    }
    &__txt {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(calc(-50% + 30px));
      width: 100%;
      padding: 15px;
      color: white;

      .btn-navy {
        padding-right: 70px;
        padding-left: 70px;

        &:hover {
          background-color: $guava;
        }
      }
    }
    &__title {
      margin-bottom: 15px;
      font-weight: 300;
      font-size: 48px;
    }
    &__subtitle {
      margin-bottom: 20px;
      font-size: 24px;
    }
  }
  .posicionamento {
    padding: 120px 0;
    margin-top: -5px;
    color: white;
    background-color: $navy;
    background-image: url("../img/LP/bg-A.png");
    background-size: auto 100%;
    background-position: top left;
    background-repeat: no-repeat;

    >.container {
      @media (min-width: 1300px) {
        padding-right: 30px;
        padding-left: 30px;

        .posicionamento__item {
          margin-right: -30px;
          margin-left: -30px;

          >.col-24 {
            padding-right: 30px;
            padding-left: 30px;
          }
        }
      }
    }
    &__item {
      &:not(:last-child) {
        margin-bottom: 150px;
      }
    }
    &__img {
      margin-top: 13px;

      img {
        max-width: 100%;
      }
    }
    &__txt {
      p {
        margin-bottom: 0;
        font-size: 18px;
      }
      .btn {
        margin-top: 30px;
      }
    }
    &__title {
      font-size: 36px;
      font-weight: 300;
      line-height: 48px;
    }
  }
  .depoimentos {
    padding: 100px 0 60px;

    .item {
      display: flex;
      align-items: flex-end;
      flex-wrap: wrap;
    }
    &__img {
      flex: 0 0 40%;
      background-image: url("../img/LP/bg-depoimento.jpg");
      background-size: auto 100%;

      img {
        max-width: 480px;
        margin-left: auto;
      }
    }
    &__content {
      flex: 0 0 60%;
      max-width: 750px;
      padding: 0 95px;
    }
    &__title {
      margin-bottom: 20px;
      color: $blue;
      font-size: 36px;
      font-weight: 300;
    }
    &__subtitle {
      margin-bottom: 30px;
      color: $navy;
      font-size: 20px;
    }
    &__txt {
      position: relative;
      color: $navy;
      font-size: 18px;
      line-height: 26px;

      &:before,
      &:after {
        content: url("../img/LP/quote.jpg");
        position: absolute;
      }
      &:before {
        top: 5px;
        left: -95px;
      }
      &:after {
        right: -80px;
        bottom: 5px;
        transform: rotate(180deg);
      }
    }
    &__signature {
      margin-bottom: 0;
      color: $navy;
      font-size: 18px;

      strong {
        display: block;
        font-size: 24px;
        line-height: 1;
      }
    }
    .owl-nav {
      @media (max-width: 991px) {
        display: none;
      }
      .owl-prev,
      .owl-next {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        color: $blue;
        font-size: 80px;
        transition: color .4s;

        &:focus {
          outline: none;
        }
        &:hover {
          color: $navy;
        }
      }
      .owl-prev {
        left: 1.5vw;
      }
      .owl-next {
        right: 1.5vw;
      }
    }
    .owl-dots {
      display: flex;
      justify-content: center;
      padding-top: 50px;

      .owl-dot {
        width: 15px;
        height: 15px;
        margin: 8px;
        background-color: white;
        border: 2px solid $navy;
        border-radius: 100%;
        transition: all .3s;

        &.active {
          background-color: $guava;
          border-color: transparent;
        }
      }
    }
  }
  .diferenciais {
    padding: 100px 0;
    background-image: url("../img/LP/bg-diferenciais.png");
    background-size: 100% auto;
    background-position: center center;

    &.vertical {
      background-size: auto 100%;
    }
    .container {
      @media (min-width: 1300px) {
        max-width: 1270px;
      }
    }
    &__title {
      margin-bottom: 60px;
      color: white;
      font-size: 48px;
      font-weight: 300;
      line-height: 1.25em;
      text-align: center;
    }
    &__list {
      display: flex;
      padding: 0;
      margin: 0 -10px;
      list-style: none;
    }
    &__item {
      flex: 0 0 33.3333%;
      display: flex;
      flex-direction: column;
      padding: 10px;

      .item {
        &-toggle {
          position: relative;
          z-index: 1;
          display: block;
          padding: 20px 40px;
          background-color: white;
          border-radius: 10px;

          &:hover {
            text-decoration: none;
          }
          &:after {
            content: "\e011";
            position: absolute;
            top: 25px;
            right: 25px;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 30px;
            height: 30px;
            color: $blue;
            font-family: $font-icons;
            font-size: 23px;
            border: 2px solid $blue;
            border-radius: 100%;
            transition: all .3s;
          }
          &.open {
            &:after {
              color: $guava;
              border-color: $guava;
              transform: rotate(45deg);
            }
          }
        }
        &-icon {
          margin-bottom: 25px;
        }
        &-title {
          margin-bottom: 0;
          color: $navy;
          font-size: 26px;
          font-weight: 300;

          span {
            display: block;
            color: $blue;
          }
        }
        &-txt {
          flex-grow: 1;
          padding: 20px 40px 30px;
          margin-top: -25px;
          color: $navy;
          background-color: white;
          border-radius: 10px;
          transition: opacity .3s;

          p {
            margin-bottom: 0;
            font-size: 17px;
            line-height: 1.5;
          }
          &.hidden {
            opacity: 0;
          }
        }
      }
    }
  }
  .calendario {
    padding: 100px 0 120px;
    color: white;
    background-image: url("../img/LP/bg-calendario.jpg");
    background-size: auto 100%;
    background-position: bottom center;

    @media (min-width: 1300px) {
      background-size: 100% auto;
    }
    @media (min-width: 1400px) {
      padding-bottom: 160px;
    }
    .container {
      @media (min-width: 992px) {
        max-width: 863px;
      }
    }
    &__title {
      margin-bottom: 25px;
      font-size: 36px;
      font-weight: 300;
      text-align: center;
      line-height: 1.3em;
    }
    p {
      margin-bottom: 40px;
      font-size: 18px;
    }
    &__form {
      .form-control {
        height: 50px;
        color: $navy;
        border: 0;

        &::placeholder {
          color: $navy;
        }
        &.is-invalid {
          border: 1px solid red;

          &::placeholder {
            color: red;
          }
        }
      }
      .form-check {
        margin-bottom: 10px;

        input {
          cursor: pointer;
        }
        label {
          display: inline;
          font-size: 12px;
          cursor: pointer;

          a {
            color: white;
            text-decoration: underline;
          }
        }
        small {
          color: red;
          background-color: white;
        }
        &.is-invalid {
          label {
            color: red;
          }
        }
			}
      .btn {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 50px;
        padding: 0;
      }
    }
  }
  .contato {
    padding: 75px 0;
    background-color: #f9f9f9;

    .container {
      display: block;

      @media (min-width: 1300px) {
        max-width: 1175px;
      }
    }
    &__title {
      margin-bottom: 60px;
      color: $blue;
      font-size: 34px;
      // font-weight: 300;
      text-align: center;
    }
    p {
      margin-bottom: 40px;
      color: $blue;
      font-size: 24px;
      text-align: center;

      strong {
        color: $guava;
        font-weight: normal;
      }
    }
    &__list {
      list-style: none;
      display: flex;
      padding-left: 0;
      margin: 0 -15px;
    }
    &__item {
      flex: 0 0 33.3333%;
      padding: 0 15px;

      .item {
        &-wrap {
          display: flex;
          flex-direction: column;
          height: 100%;
          padding-bottom: 30px;
          background-color: white;
          border-radius: 12px;
          box-shadow: 0 0 15px rgba(#282828, .2);
        }
        &-img {
          max-width: 100%;
        }
        &-title {
          padding: 0 25px;
          margin-top: 25px;
          margin-bottom: 15px;
          color: $navy;
          font-size: 20px;
        }
        &-desc {
          flex-grow: 1;
          padding: 0 25px;
          margin-bottom: 20px;
          font-size: 16px;
        }
      }
      .btn-link {
        display: inline-flex;
        align-items: center;
        margin: 10px 25px;
        color: $blue;

        .icon {
          margin-left: 5px;
          color: $guava;
          text-decoration: none;
          transition: margin-left .3s;
        }
        &:hover {
          text-decoration: none;

          .icon {
            margin-left: 10px;
          }
        }
      }
    }
  }
  .footer {
    padding-bottom: 25px;
    color: #545454;
    background-color: #f9f9f9;

    &__brand {
      margin-bottom: 50px;
      text-align: center;
    }
    &__social {
      font-size: 16px;
      text-align: center;

      p {
        margin-bottom: 0;
      }
      .social__list {
        display: inline-flex;
        padding-left: 0;
        list-style: none;

        .list-item {
          padding: 10px;
        }
        .icon {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 52px;
          height: 52px;
          color: white;
          font-size: 25px;
          background-color: $blue;
          border-radius: 100%;
          transition: background-color, .4s;

          &:hover {
            text-decoration: none;
            background-color: $navy;
          }
        }
      }
    }
    &__copyright {
      font-size: 13px;
      text-align: center;

      a {
        color: inherit;
      }
    }
  }

  .modal-contato {
    color: white;

    .modal-dialog {
      @media (min-width: 768px) {
        max-width: 600px;
      }
    }
    .modal-content {
      padding: 50px 75px 30px;
      background-color: $blue;
      border-radius: 10px;
    }
    .close {
      position: absolute;
      top: 10px;
      right: 10px;
      transform: rotate(45deg);
      color: white;
      font-size: 30px;
      opacity: 1;

      &:focus {
        outline: none;
      }
    }
    .modal-title {
      margin-bottom: 20px;
      font-size: 36px;
      font-weight: 300;
    }
    p {
      font-size: 18px;
    }
    em {
      font-size: 14px;
    }
    .form {
      padding-top: 20px;

      .form-label {
        font-size: 20px;
      }
      .form-control {
        padding-top: 10px;
        padding-bottom: 8px;
        color: $navy;

        &::placeholder {
          color: $navy;
        }
        &.is-invalid {
          border: 1px solid red;

          &::placeholder {
            color: red;
          }
        }
      }
      textarea.form-control {
        margin-top: 30px;
      }
      .form-check {
        padding-left: 0;

        input,
        label {
          display: inline;
          vertical-align: middle;
        }
        a {
          color: white;
          text-decoration: underline;
        }
        small {
          display: block;
          padding: 2px 5px;
          margin-top: 5px;
          color: red;
          background-color: white;
        }
      }
      .btn {
        padding: 8px 45px;
      }
    }
  }
  .modal-video {
    .close {
      position: absolute;
      top: -30px;
      right: -30px;
      transform: rotate(45deg);
      color: white;
      font-size: 30px;
      opacity: 1;

      &:focus {
        outline: none;
      }
    }
    video {
      width: 100%;
    }
  }
  .modal-cookies {
    color: white;

    .modal-dialog {
      @media (min-width: 768px) {
        max-width: 600px;
      }
    }
    .modal-content {
      padding: 30px 25px 30px;
      background-color: $blue;
      border-radius: 10px;
    }
    .close {
      position: absolute;
      top: 10px;
      right: 10px;
      transform: rotate(45deg);
      color: white;
      font-size: 30px;
      opacity: 1;

      &:focus {
        outline: none;
      }
    }
    .modal-title {
      margin-bottom: 20px;
      font-size: 25px;
      font-weight: 300;
      text-transform: uppercase;
    }

    .switch {
      position: relative;
      display: inline-block;
      width: 50px;
      height: 30px;
      min-width: 50px;
      margin-right: 10px;
      margin-bottom: 0;

      input {
        opacity: 0;
        width: 0;
        height: 0;

        &:checked + .slider {
          background-color: $navy;
        }
        &:focus + .slider {
          box-shadow: 0 0 1px $navy;
        }
        &:checked + .slider:before {
          -webkit-transform: translateX(22px);
          -ms-transform: translateX(22px);
          transform: translateX(22px);
        }
      }
    }
    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #ccc;
      -webkit-transition: .4s;
      transition: .4s;

      &:before {
        position: absolute;
        content: "";
        height: 22px;
        width: 22px;
        left: 4px;
        bottom: 4px;
        background-color: white;
        -webkit-transition: .4s;
        transition: .4s;
      }
      &.round {
        border-radius: 34px;

        &:before {
          border-radius: 50%;
        }
      }
    }
  }
  .cookies {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 100;
    transform: translateY(100%);
    display: block;
    width: 100%;
    padding: 15px 0;
    background-color: rgba($blue, .9);
    transition: transform .4s;

    &.open {
      transform: translateY(0);
    }
    .container-fluid {
      max-width: 800px;
    }
    p {
      color: white;
      font-size: 13px;
      text-align: center;
    }
    &--links {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;

      a {
        padding: 2px 10px;
        color: white;
        font-size: 14px;
        font-weight: 600;
      }
    }
  }
}

@media (min-width: 1600px) {
  .landing {
    .header {
      // top: -115px;

      &__logo {
        // width: 220px;
      }
      &__nav {
        margin-left: -20px;
        margin-right: -20px;

        .nav-link {
          padding: 5px 20px;
          // font-size: 24px;
        }
      }
    }
  }
}
@media (min-width: 1300px) {
  .landing {
    .header {
      &__logo {
        width: 200px;
      }
      &__nav {
        .nav-link {
          font-size: 20px;
        }
      }
    }
    .diferenciais {
      &__item {
        .item {
          &-title {
            font-size: 28px;
          }
        }
      }
    }
  }
}
@media (min-width: 992px) {
  .landing {
    .header {
      align-items: center;
      padding: 25px 3vw 15px;

      &__logo {
        width: 180px;
      }
      &__nav {
        margin: 0 -15px 20px;

        .nav-link {
          padding: 5px 15px;
          font-size: 18px;
          border-radius: 8px;
        }
      }
    }
  }
}

@media (max-width: 1199px) {
  .landing {
    .banner {
      &__txt {
        .btn {
          padding: 12px 50px;
        }
      }
      &__title {
        font-size: 38px;
      }
      &__subtitle {
        font-size: 20px;
      }
    }
    .depoimentos {
      &__content {
        padding: 0 70px;
      }
      &__txt {
        font-size: 16px;

        &:before {
          left: -70px;
        }
        &:after {
          right: -55px;
        }
      }
    }
    .diferenciais {
      &__title {
        font-size: 42px;
      }
      &__item {
        .item {
          &-icon {
            img {
              max-height: 80px;
            }
          }
          &-title {
            font-size: 21px;
          }
          &-txt {
            font-size: 15px;
          }
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .landing {
    .posicionamento {
      padding: 60px 0;
      background-size: 100% auto;

      &__item {
        &:not(:last-child) {
          margin-bottom: 50px;
        }
        &:nth-child(even) {
          .col-24:first-child {
            order: 2;
          }
        }
      }
    }
    .depoimentos {
      padding: 50px 0;

      &__img {
        order: 2;
        flex: 0 0 100%;
        max-width: none;
      }
      &__content {
        flex: 0 0 100%;
        max-width: none;
        padding: 0 70px 30px;
      }
      &__txt {
        &:before {
          left: -55px;
        }
        &:after {
          right: -55px;
        }
      }
      &__title {
        margin-bottom: 15px;
      }
      &__subtitle {
        margin-bottom: 20px;
      }
      .owl-dots {
        padding-top: 30px;
      }
    }
    .diferenciais {
      padding: 60px 0;

      &__title {
        font-size: 38px;
      }
      &__list {
        flex-wrap: wrap;
      }
      &__item {
        flex: 0 0 100%;

        .item {
          &-title {
            font-size: 28px;
          }
          &-txt {
            &.hidden {
              display: none;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 767px) {
  .landing {
    .header {
      align-items: flex-start;
      padding-right: 15px;
      padding-left: 15px;

      &__nav {
        position: fixed;
        top: 0;
        right: -290px;
        z-index: 10;
        flex-direction: column;
        width: 290px;
        height: 100%;
        padding: 60px 0 30px;
        margin: 0;
        background-color: $blue;
        transition: all .4s;

        &.open {
          right: 0;
        }
        .nav-link {
          display: block;
          padding: 5px 30px;
          font-size: 20px;
          text-align: right;

          &:hover, &:focus {
            background-color: transparent;
          }
        }
      }
      .nav-toggle {
        display: inline-flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: space-between;

        .bar {
          z-index: 11;
          display: inline-block;
          height: 3px;
          margin: 3px;
          background-color: white;
          border-radius: 2px;

          &:nth-child(1) {
            width: 35px;
          }
          &:nth-child(2) {
            width: 25px;
          }
          &:nth-child(3) {
            width: 20px;
          }
        }
        &.open {
          &:after {
            content: "";
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba($navy, .95);
          }
        }
      }
    }
    .banner {
      overflow: hidden;

      &__video {
        display: inline-block;
        width: auto;
        height: 440px;
        margin-left: 50%;
        transform: translateX(-50%);
      }
      &__title {
        font-size: 40px;
      }
    }
    .posicionamento {
      padding: 40px 0;

      &__item {
        &:not(:last-child) {
          margin-bottom: 30px;
        }
        &:nth-child(even) {
          .col-24:first-child {
            order: 2;
          }
        }
      }
    }
    .diferenciais {
      &__title {
        margin-bottom: 30px;
        font-size: 28px;
      }
      &__item {
        .item {
          &-icon {
            margin-bottom: 10px;

            img {
              max-height: 70px;
            }
          }
          &-title {
            font-size: 25px;
          }
        }
      }
    }
    .calendario {
      padding: 40px 0 100px;
    }
    .contato {
      padding: 40px 0;

      &__title {
        margin-bottom: 30px;
        font-size: 30px;
      }
      p {
        font-size: 21px;
        margin-bottom: 20px;
      }
      &__list {
        flex-wrap: wrap;
      }
      &__item {
        flex: 0 0 100%;
        padding: 15px;
      }
    }
    .modal-contato {
      .modal-content {
        padding: 40px 15px 30px;
      }
    }
  }
}
@media (max-width: 575px) {
  .landing {
    .header {
      &__nav {
        .nav-link {
          padding: 5px 15px;
        }
      }
    }
    .banner {
      &__video {
        height: 350px;
      }
      &__txt {
        .btn {
          padding: 10px 40px;
          font-size: 14px;
        }
      }
      &__title {
        font-size: 28px;
      }
      &__subtitle {
        font-size: 14px;
      }
    }
    .posicionamento {
      &__txt {
        p {
          font-size: 14px;
        }
      }
      &__title {
        font-size: 30px;
      }
    }
    .depoimentos {
      &__content {
        padding: 0 15px 20px;
      }
      &__txt {
        padding: 50px 0;

        &:before {
          left: 0;
        }
        &:after {
          right: 0;
        }
      }
      &__title {
        margin-bottom: 15px;
      }
      &__subtitle {
        margin-bottom: 20px;
      }
      .owl-dots {
        padding-top: 20px;
      }
    }
    .diferenciais {
      &__item {
        .item {
          &-title {
            font-size: 21px;
          }
        }
      }
    }
    .calendario {
      &__title {
        font-size: 26px;
      }
      p {
        font-size: 16px;
      }
    }
  }
}