.embarque-certo {
  color: #545454;
  background-color: #f9f9f9;

  .btn {
    padding: 15px 25px;
    border-radius: 12px;

    &.btn-sm {
      padding: 5px;
    }
    &.btn-default {
      color: #545454;
      font-size: 16px;
      background-color: #c8c8c8;

      &:hover,
      &:focus,
      &.active {
        color: white;
        background-color: #057dc8;
      }
    }
  }
  a:not([class]) {
    color: #057dc8;
  }
  &--header {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    padding: 30px 3vw 10px;
  }
  .banner {
    position: relative;
    min-height: 342px;
    margin-bottom: 40px;
    overflow: hidden;

    img {
      width: 100%;
    }
    figcaption {
      position: absolute;
      bottom: 30px;
      left: 50%;
      transform: translateX(-50%);
      width: 1000px;
      padding: 10px 15px 20px;
      color: white;
      font-size: 24px;
      font-weight: 300;
      line-height: 1.2;
      text-align: center;
      // background-color: rgba(#021859, .8);
      // border-radius: 6px;

      strong {
        display: block;
        color: $guava;
        font-size: 56px;
        font-weight: 300;
      }
    }
  }
  .container.container__sm {
    max-width: 900px;
  }
  &--title {
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--subtitle {
    font-size: 20px;
    text-align: center;

    a {
      color: #057dc8;
    }
  }
  .steps {
    margin: 30px 0 50px;
    background-color: white;

    &--list {
      display: flex;
      justify-content: center;
      list-style: none;
      padding: 0;
      margin: 0 -15px;

      .list-item {
        padding: 20px 15px 25px;
      }
      .list-link {
        position: relative;
        display: block;
        color: #545454;
        line-height: 1.2;
        text-align: center;

        &:hover {
          text-decoration: none;
        }
        &.disabled {
          color: #c8c8c8;

          &:hover {
            cursor: default;
          }
        }
        &.active {
          color: #057dc8;

          &:hover {
            cursor: default;
          }
          &:after {
            content: "";
            position: absolute;
            bottom: -15px;
            left: 0;
            width: 100%;
            height: 6px;
            background-color: #eaeaea;
            border-radius: 10px;
          }
        }
      }
    }
    h2 {
      padding: 20px 0;
      margin: 0;
      color: $blue;
      font-size: 26px;
      text-align: center;
    }
  }
  .step-title {
    font-weight: 300;
    font-size: 20px;
    text-align: center;
  }
  .step-btns {
    margin-top: 40px;
    text-align: center;

    .btn {
      min-width: 130px;

      + .btn {
        margin-left: 20px;
      }
    }
  }
  form {
    .form-control {
      color: #057dc8;
      border-radius: 12px;

      &::placeholder {
        color: #057dc8;
      }
    }
    .input-group-append {
      .btn {
        border-top-right-radius: 12px;
        border-bottom-right-radius: 12px;
      }
    }
  }
  .step-one {
    max-width: 500px;
    margin: 0 auto 20px;

    form {
      max-width: 400px;
      margin: 0 auto;
    }
  }
  .step-proposta {
    max-width: 500px;
    padding-top: 20px;
    margin: 0 auto;

    &.hidden {
      display: none;
    }
    .step-title {
      font-weight: 300;
      font-size: 26px;
    }
    form {
      max-width: 400px;
      margin: 0 auto;

      .form-control {
        height: 56px;
        border-radius: 12px;
      }
    }
  }
  .step-two {
    max-width: 550px;
    margin: 0 auto 20px;

    form {
      .input-group-append {
        .btn {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }
  .step-three {
    max-width: 515px;
    margin: 0 auto 20px;

    .step-title {
      font-weight: 300;
      max-width: 500px;
      margin: 0 auto 40px;
      font-size: 26px;
    }
    .form-radio {
      display: flex;
      align-items: center;

      &:not(:last-child) {
        margin-bottom: 16px;
      }
      .radio {
        position: relative;
        display: flex;
        margin-right: 15px;

        &.checked {
          label {
            &:after {
              font-size: 40px;
            }
          }
        }
        input {
          position: absolute;
          top: 0;
          left: 0;
          opacity: 0;
        }
        label {
          width: 50px;
          height: 50px;
          margin: 0;
          border: 1px solid #ced4da;
          border-radius: 12px;
          cursor: pointer;

          &:after {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            content: "\e01b";
            color: #057dc8;
            font-family: $font-icons;
            font-size: 0;
            transition: font-size .3s;
          }
        }
      }
      label {
        margin: 0;
        line-height: 1.2;
      }
    }
  }
  .step-four {
    .step-title {
      font-weight: 300;
      max-width: 700px;
      margin: 0 auto 10px;
      font-size: 26px;
      text-align: center;

      &.color {
        color: $blue;
      }
    }
    .step-subtitle {
      font-size: 20px;
      font-weight: 300;
      text-align: center;
    }
    .step-box {
      padding: 25px 15px 15px;
      margin-top: 35px;
      margin-bottom: 25px;
      background-color: #f2f2f2;
      border-radius: 12px;
    }
    form {
      max-width: 250px;
      margin: 0 auto;

      .form-control {
        height: 100%;
        color: $blue;
        text-align: center;

        &::-webkit-inner-spin-button {
          -webkit-appearance: none;
        }
      }
      .select2 {
        width: 100%;
        padding-top: 5px;
      }
      .select2-selection {
        display: flex;
        align-items: center;
        width: 100%;
        height: 38px;
        padding: .375rem .75rem;
        color: #057dc8;
        font-size: 1rem;
        line-height: 1.5;
        border-radius: 12px;
      }
      .select2-selection__arrow {
        height: 100%;
      }
      label {
        display: block;
        margin: 0;
      }
      .btn {
        margin-top: 20px;
      }

      &:first-of-type {
        max-width: none;
      }
    }
    .table {
      border-collapse:separate;
      border-spacing: 0 10px;

      tr {
        margin-bottom: 10px;
        background-color: #c8c8c8;
      }
      th {
        padding: 5px 10px;
        vertical-align: middle;
        font-size: 16px;
        white-space: nowrap;
        border: 0;
        border-radius: 12px 0 0 12px;
      }
      td {
        padding: 5px 10px;
        vertical-align: middle;
        font-size: 14px;
        line-height: 1.2;
        text-align: center;
        white-space: nowrap;
        border: 0;

        &:last-child {
          border-radius: 0 12px 12px 0;
        }
        strong {
          color: white;
          font-size: 18px;

          &.color {
            color: $blue;
          }
        }
      }
      .icon {
        color: $blue;
        font-size: 24px;

        &:hover {
          color: $guava;
          text-decoration: none;
        }
      }
    }
  }
  .step-five {
    max-width: 800px;
    margin: 0 auto;

    .step-title {
      font-weight: 300;
      margin: 0 auto 30px;
      font-size: 26px;
      text-align: center;

      &.color {
        color: $blue;
      }
    }
    form {
      .form-checkbox {
        display: flex;
        align-items: center;

        &:not(:last-child) {
          margin-bottom: 16px;
        }
        .checkbox {
          position: relative;
          display: flex;
          margin-right: 15px;

          input:checked {
            ~ label {
              &:after {
                font-size: 40px;
              }
            }
          }
          input {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
          }
          label {
            width: 50px;
            height: 50px;
            margin: 0;
            border: 1px solid #ced4da;
            border-radius: 12px;
            cursor: pointer;

            &:after {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              content: "\e01b";
              color: #057dc8;
              font-family: $font-icons;
              font-size: 0;
              transition: font-size .3s;
            }
          }
        }
        label {
          margin: 0;
          line-height: 1.2;
        }
      }
    }
  }
  .step-six {
    max-width: 520px;
    margin: 0 auto;
  }
  .container-type {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -15px 30px;

    .type {
      &-item {
        padding: 15px;
        text-align: center;

        p {
          margin-bottom: 5px;
        }
        .btn {
          display: inline-flex;
          align-items: center;
          padding: 10px;
          color: white;
          font-size: 20px;

          img {
            width: 40px;
            margin-left: 10px;
          }
          + .btn {
            margin-left: 20px;
          }
        }
      }
    }
  }
  .container-flow {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -10px 30px;

    .flow {
      &-item {
        flex: 0 0 33.3333%;
        padding: 10px;
        font-size: 15px;
        text-align: center;

        p {
          margin-bottom: 10px;
        }
        .input-qtd {
          margin: 0 auto;
        }
        &.disabled {
          .flow-deadline {
            color: #bdbdbd;
          }
        }
      }
      &-deadline {
        margin-top: 10px;
        line-height: 1;

        span {
          display: block;
          color: #bdbdbd;
        }
      }
    }
  }
  .list-propostas {
    max-width: 400px;
    padding-left: 0;
    margin: 0 auto 40px;
    list-style: none;

    &--item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 15px;
      margin-bottom: 5px;
      background-color: #c8c8c8;
      border-radius: 12px;

      .icon {
        color: #057dc8;
        font-size: 24px;

        &:hover {
          color: $guava;
          text-decoration: none;
        }
      }
    }
  }
  .text-box {
    padding: 15px 10px 15px 20px;
    margin-bottom: 40px;
    background-color: white;
    border: 1px solid #c8c8c8;
    border-radius: 12px;

    &--scroll {
      max-height: 375px;
      padding-right: 10px;
      overflow: auto;

      &::-webkit-scrollbar {
        width: 8px;
      }
      &::-webkit-scrollbar-track {
      box-shadow: inset 0 0 8px rgba(#c8c8c8, .3);
      border-radius: 8px;
      }
      &::-webkit-scrollbar-thumb {
        background: #c8c8c8;
        border-radius: 8px;
      }
      &::-webkit-scrollbar-thumb:hover {
        background: darken(#c8c8c8, 3%);
        cursor: pointer;
      }
    }
  }
  .resumo {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 700px;
    margin: 30px auto 0;

    &-item {
      display: flex;
      align-items: center;
      flex: 0 0 33.3333%;
      padding: 15px;

      input:disabled {
        width: 100px;
        height: 50px;
        margin-right: 10px;
        color: white;
        text-align: center;
        background-color: $blue;
        border: 0;
        border-radius: 12px;
      }
      p {
        margin-bottom: 0;
        font-size: 16px;
        line-height: 1.2;
        white-space: nowrap;

        strong {
          display: block;
          color: $blue;
        }
      }
    }
  }
  &--footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 25px 3vw 30px;
    margin-top: 70px;
    background-color: white;

    .footer {
      &--logo {
        flex: 0 0 200px;
        max-width: 200px;
        margin-bottom: 0;
      }
      &--list {
        display: flex;
        align-items: center;
        justify-content: center;
        list-style: none;
        padding: 15px;
        margin: 0 -10px;

        li {
          padding: 0 10px;
          color: #545454;
          font-size: 15px;
          line-height: .9;
          text-align: center;

          &:not(:last-child) {
            border-right: 1px solid #545454;
          }
          a {
            display: inline-block;
            color: #545454;
            line-height: .9;
          }
        }
      }
    }
    .social-list {
			display: flex;
			justify-content: flex-end;
			padding-left: 0;
			margin: 0 -6px;
			list-style: none;

			li {
				padding: 6px;

				a {
					display: flex;
					align-items: center;
					justify-content: center;
					padding: 10px;
					color: white;
					font-size: 25px;
					background-color: $blue;
					border-radius: 100%;
					transition: all .3s;

					&:hover {
						text-decoration: none;
						background-color: $navy;
					}
				}
			}
		}
  }
}

@media (min-width: 1200px) {
  .embarque-certo {
    &--footer {
      .social-list {
        width: 200px;
      }
    }
  }
}
