@media (max-width: 1599px) {
  .embarque-certo {
    .banner {
      img {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: auto;
        height: 100%;
      }
    }
  }
}
@media (max-width: 1199px) {
  .embarque-certo {
    &--header {
      padding: 30px 30px 10px;
    }
    // .banner {
    //   figcaption {
    //     width: 800px;
    //     font-size: 36px;
    //   }
    // }
    &--footer {
      padding-right: 15px;
      padding-left: 15px;

      .footer {
        &--logo {
          flex: 0 0 150px;
        }
        &--list {
          padding-right: 0;
          padding-left: 0;

          li {
            font-size: 14px;
          }
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .embarque-certo {
    &--header {
      .header--logo {
        img {
          width: 201px;
        }
      }
    }
    .banner {
      min-height: 300px;

      figcaption {
        width: 100%;

        strong {
          font-size: 36px;
        }
      }
    }
    &--footer {
      flex-direction: column;

      .footer {
        &--logo {
          flex: 0 0 auto;
          margin-bottom: 10px;
        }
      }
      .social-list {
        justify-content: center;
      }
    }
  }
}
@media (max-width: 767px) {
  .embarque-certo {
    &--header {
      .header--logo {
        img {
          width: 161px;
        }
      }
    }
    // .banner {
    //   min-height: 250px;

    //   figcaption {
    //     width: 330px;
    //     font-size: 24px;
    //     line-height: 1.2;
    //   }
    // }
    .steps {
      .container {
        max-width: none;
      }
    }
    .container-flow {
      .flow-item {
        flex: 0 0 50%;
      }
    }
    .resumo {
      &-item {
        flex: 0 0 50%;
      }
    }
    &--footer {
      .footer--list {
        li {
          flex: 0 0 auto;
        }
      }
    }
  }
}
@media (max-width: 575px) {
  .embarque-certo {
    .steps {
      &--list {
        justify-content: flex-start;
        overflow-x: auto;

        .list-link {
          white-space: nowrap;
        }
      }
    }
    .container-flow {
      .flow-item {
        flex: 0 0 100%;
      }
    }
    .resumo {
      margin: 20px -15px 0;

      &-item {
        flex-direction: column;
        align-items: center;
        flex: 0 0 130px;

        input:disabled {
          margin: 0;
        }
        p {
          margin-top: 6px;
          text-align: center;
        }
      }
    }
    &--footer {
      .footer {
        &--logo {
          max-width: 160px;
        }
        &--list {
          li {
            flex: 0 0 100%;
            padding: 10px;
          }
        }
      }
    }
  }
}