.category {
	padding: 60px 0;
	background-image: url("../img/bg-category.png");
	background-size: 100% auto;
	background-position: center;
	background-repeat: no-repeat;
	overflow: hidden;

	@media (max-width: 767px) {
		background-image: none;
	}
}
.owl-category.owl-carousel {
	@media (min-width: 576px) {
		padding: 0 15px;
	}

	.owl-stage {
		display: flex;
	}
	.owl-item {
		padding-top: 20px;
		padding-left: 15px;
		padding-right: 15px;
		padding-bottom: 15px;
		transition: all .8s;

		&:hover {
			padding-top: 0;
			margin-bottom: 20px;
		}
	}
	.owl-nav {
		// @media (min-width: 768px) {
		// 	display: block;
		// }

		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			display: flex;
			justify-content: center;
			align-items: center;
			padding: 8px !important;
			background-color: $blue;
			border-radius: 100%;
			transition: all .4s;

			&:hover {
				background-color: darken($blue, 10%);
			}
			&:focus {
				outline: none;
			}
			.icon {
				color: white;
				font-size: 20px;
			}
		}
		.owl-prev {
			left: -10px;

			&:hover {
				left: -15px;
			}
		}
		.owl-next {
			right: -10px;

			&:hover {
				right: -15px;
			}
		}
	}
	.owl-dots {
		display: flex;
		justify-content: center;
		overflow: hidden;

		@media (min-width: 768px) {
			display: none;
		}
		.owl-dot {
			position: relative;
			width: 16px;
			height: 16px;
			margin: 5px;
			border: 2px solid $navy;
			background-color: transparent;
			border-radius: 100%;
			transition: all .3s;

			&.active {
				background-color: $navy;
				border-color: $navy;
			}
		}
	}
	.item {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		height: 100%;
		padding: 30px;
		color: $font-color;
		font-size: 15px;
		text-align: center;
		background-color: white;
		border-radius: 14px;
		box-shadow: 0 0 10px rgba(#282828, .1);
		transition: all .4s;

		&:hover {
			// padding: 30px 30px 60px;
			color: white;
			text-decoration: none;
			background-color: $navy;

			.category {
				&--title {
					color: white;
				}
				&--mais {
					color: white;

					.icon {
						color: white;
						margin-left: 10px;
					}
				}
			}
		}
		// p:last-child {
		// 	margin-bottom: 0;
		// }
	}
	.category {
		&--img {
			margin-bottom: 15px;
			text-align: center;

			img {
				display: inline-block;
				width: auto;
				max-width: 100%;
			}
		}
		&--title {
			color: $navy;
			font-size: 22px;
			transition: all .4s;
		}
		&--mais {
			display: inline-flex;
			align-items: center;
			justify-self: flex-end;
			margin: auto 0 0;
			color: $blue;
			font-size: 14px;
			font-weight: bold;
			transition: all .4s;

			.icon {
				color: $navy;
				font-size: 13px;
				margin-left: 5px;
				transition: all .4s;
			}
		}
	}
}

@media (min-width: 1200px) {
	.owl-category.owl-carousel {
		.owl-nav {
			.owl-prev {
				left: -30px;

				&:hover {
					left: -35px;
				}
			}
			.owl-next {
				right: -30px;

				&:hover {
					right: -35px;
				}
			}
		}
	}
}
@media (min-width: 1300px) {
	.owl-category.owl-carousel {
		.owl-nav {
			.owl-prev {
				left: -90px;

				&:hover {
					left: -95px;
				}
			}
			.owl-next {
				right: -90px;

				&:hover {
					right: -95px;
				}
			}
		}
	}
}