@charset "UTF-8";

@font-face {
  font-family: "alianca";
  src:url("../fonts/alianca.eot");
  src:url("../fonts/alianca.eot?#iefix") format("embedded-opentype"),
    url("../fonts/alianca.woff") format("woff"),
    url("../fonts/alianca.ttf") format("truetype"),
    url("../fonts/alianca.svg#alianca") format("svg");
  font-weight: normal;
  font-style: normal;

}

// Catchall baseclass
.icon {
  position: relative;
  top: 0;
  display: inline-block;
  vertical-align: middle;
  font-family: $font-icons;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

