.lp {
	font-family: 'Maersk Text';
	background-color: #f0f0f0;

	&-header {
		position: fixed;
		top: 0;
		left: 0;
		transform: translateY(-100%);
		z-index: 10;
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		padding: 15px 30px 15px;
		background-color: transparent;
		transition: all .4s;

		@media (min-width: 992px) {
			padding: 20px 3vw 20px;
		}
		img {
			width: 150px;

			@media (min-width: 1200px) {
				width: 180px;
			}
		}
		&.bg {
      background-color: rgba(#021859, .6);
    }
    &.show {
      transform: none;
    }
	}
	&-fav {
		display: none;
		position: absolute;
		z-index: 4;
		top: 10px;
		right: 20px;
		max-width: 325px;
		padding: 10px;
		color: white;
		font-weight: 300;
		text-align: center;
		background-color: rgba(#1874c6, .9);
		border-radius: 18px;

		@media (min-width: 992px) {
			right: calc(3vw - 10px);
		}
		@media (max-width: 525px) {
			// left: 10px;
			// max-width: none;
			font-size: 14px;
		}
		&.open {
			display: block;
		}
		strong {
			font-weight: bold;
		}
		&--toggle {
			position: relative;
			z-index: 5;
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: none;
			color: white;
			white-space: nowrap;

			@media (min-width: 992px) {
				width: 252px;
			}
			&:hover {
				color: white;
				text-decoration: none;
			}
			&-icon {
				flex: 0 0 50px;
				width: 50px;
				padding: 5px;
				background-color: white;
				border-radius: 100%;

				@media (min-width: 992px) {
					flex: 0 0 56px;
					width: 56px;
					padding: 8px;
				}

				img {
					max-width: 100%;
				}
			}
			.lp-fav--title {
				@media (max-width: 991px) {
					display: none;
				}
			}
		}
		&--header {
			display: flex;
			align-items: center;
			justify-content: flex-end;
			height: 50px;
			padding: 10px;
			margin-bottom: 15px;

			@media (min-width: 992px) {
				height: 56px;
			}
			.lp-fav--title {
				padding-right: 60px;

				@media (min-width: 992px) {
					opacity: 0;
				}
			}
		}
		&--title {
			margin-bottom: 0;
			font-size: 20px;
		}
	}
	&-banner {
		position: relative;
		margin-bottom: 0;
		overflow: hidden;

		.item {
			text-align: center;
		}
		&--txt {
			position: absolute;
			bottom: 3vw;
			left: 3vw;
			z-index: 2;
			color: white;
			text-shadow: 1px 1px 8px #021859;

			@media (max-width: 1199px) {
				bottom: 30px;
				left: 30px;
			}
			h1 {
				font-size: 42px;
				font-weight: 300;
			}
			h2 {
				margin-bottom: 0;
				font-size: 24px;
			}
		}
	}
	.calendar {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 50px 50px;
		margin: 0 -10px;

		&--filter {
			display: flex;
			align-items: center;

			.d-flex {
				flex-grow: 1;
			}
			.filter {
				&--events,
				&--view {
					display: flex;
					align-items: center;
					padding: 10px 0;
					margin: 0 -15px;
					list-style: none;
				}
				&--events {
					.filter-item {
						padding: 10px 15px;

						img {
							margin-right: 10px;
						}
					}
				}
				&--view {
					.filter-item {
						flex-direction: column;
						padding: 5px 15px;

						img {
							margin-bottom: 5px;
						}
					}
					@media (max-width: 1199px) {
						display: none;
					}
				}
				&--link {
					display: flex;
					align-items: center;
					color: $blue;
					font-size: 15px;
					line-height: 1;

					img {
						margin-right: 10px;
					}
					&:hover {
						text-decoration: none;
					}
					@media (min-width: 576px) and (max-width: 767px) {
						flex-wrap: wrap;
						justify-content: center;
						text-align: center;

						img {
							margin: 0 auto 5px;
						}
					}
				}
				&-item {
					display: flex;
					align-items: center;
					justify-content: center;
					height: 100%;
					color: #000925;
					font-size: 15px;
					line-height: 1;
					border-radius: 15px;
					cursor: pointer;


					&.active {
						background-color: #d7d7d7;
					}
				}
			}
			.spacer {
				position: relative;
				padding: 15px 50px;
				height: 60px;

				&:after {
					content: "";
					position: absolute;
					top: 0;
					left: 50%;
					transform: translateX(-50%);
					width: 1px;
					height: 100%;
					background-color: #7c7c7c;
				}
				@media (max-width: 575px) {
					display: none;
				}
			}
		}
		&-item {
			flex: 0 0 25%;
			padding: 10px;
			font-family: "Calibri";

			.fc-header-toolbar {
				padding-top: 10px;
				margin-bottom: 0;
				color: #000925;
				text-transform: capitalize;

				h2 {
					font-size: 25px;
				}
			}
			.fc-month-view {

				> table {
					overflow: hidden;
					border-radius: 15px;
					box-shadow: 0 0 20px rgba(black, .1);

					> thead {
						th {
							padding-top: 10px;
							padding-bottom: 10px;
							font-weight: normal;
							text-transform: uppercase;

							&:last-child {
								color: #1874c6;
							}
						}
					}
				}
			}
			.fc table {
				font-size: 14px;
				background-color: white;
				// border-radius: 20px;
			}
			.fc th, .fc td {
				text-align: center;
				vertical-align: middle;
				border-color: transparent;

				.tem-evento {
					color: #1874c6;
					font-weight: bold;
				}
			}
			.fc-unthemed .fc-today {
			    background: transparent;
			}
			.fc-basic-view tbody .fc-row {
			    min-height: 40px;
			}
			.fc-day-number {
				color: #000925;
			}
			.fc-sun .fc-day-number {
				color: #1874c6;
			}
			.fc-day-header {
				color: #545454;
			}
			.fc-day-grid-event>.fc-content {
				display: none;
			}
			.fc .fc-event-container {
				vertical-align: top;
			}
			.fc-view .fc-event {
			    height: 2px;
			    margin: 1px;
			    background-color: #021859;
			    border: none;
			    border-radius: 0;
			}
			&:nth-child(2) {
				flex: 0 0 40%;

				@media (max-width: 1199px) {
					flex: 0 0 100%;
					max-width: 550px;
				}
				.fc table {
					font-size: 16px;
				}
				.fc-header-toolbar {
					padding-top: 0;

					h2 {
						font-size: 32px;
					}
				}
				.fc-basic-view tbody .fc-row {
				    min-height: 50px;
				}
				.fc-view .fc-event {
				    height: 2px;
				}
			}
		}
		&--btns {
			.btn-prev,
			.btn-next {
				position: absolute;
				top: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				padding: 12px;
				color: white;
				font-size: 15px;
				background-color: #1874c6;
				border-radius: 100%;
				transition: all .4s;

				&:hover {
					background-color: #000925;
					text-decoration: none;
				}
			}
			.btn-prev {
				left: 30px;
			}
			.btn-next {
				right: 30px;
			}
		}
		&--caption {
			display: flex;
			padding-top: 30px;
			padding-left: 0;
			padding-bottom: 50px;
			margin: 0 -15px;
			list-style: none;
			border-top: 1px solid #b6b6b6;

			.caption {
				&--item {

					.color {
						display: inline-block;
						width: 10px;
						height: 10px;
						margin-right: 5px;
						background-color: #021859;
						border-radius: 100%;

						&#color01 {
							background-color: #021859;
						}
						&#color02 {
							background-color: #057dc8;
						}
						&#color03 {
							background-color: #ff6d70;
						}
						&#color04 {
							background-color: #545454;
						}
					}
				}
			}
		}
		&--destaque {
			position: relative;
			padding-top: 70px;
			margin-top: 70px;
			background-color: #001159;

			&:before {
				content: "";
				position: absolute;
				top: -69px;
				left: 50%;
				transform: translateX(-50%);
				z-index: 1;
				width: 14px;
				height: 130px;
				background-image: url("../img/LP/flow.png");
				background-size: 100%;
				background-repeat: no-repeat;
			}
			.container {
				@media (min-width: 1300px) {
					max-width: 1275px;
				}
			}
			.destaque {
				&--title {
					margin-bottom: 20px;
					color: #ff6d70;
					font-size: 28px;
					text-align: center;
					text-transform: uppercase;
				}
			}
			.owl-destaque {
				.owl-dots {
					display: flex;
					align-items: center;
					justify-content: center;
					height: 64px;
					padding: 30px 15px 20px;

					.owl-dot {
						position: relative;
						display: flex;
						align-items: center;
						justify-content: center;
						width: 14px;

						span {
							display: inline-block;
							width: 10px;
							height: 10px;
							border: 2px solid white;
							border-radius: 100%;
							transition: all .4s;
						}
						&:focus {
							outline: none !important;
						}
						&.active {
							span {
								width: 14px;
								height: 14px;
								border: 3px solid $blue;
							}
							&:not(:last-child) {
								&:after {
									background-color: $blue;
								}
							}
						}
						&:not(:last-child) {
							margin-right: 20px;

							&:after {
								content: "";
								position: absolute;
								left: 18px;
								top: 50%;
								transform: translateY(-50%);
								width: 12px;
								height: 2px;
								background-color: white;
							}
						}
					}
				}
			}
		}
		&--combo {
			margin-top: 20px;

			.form-select {
				width: 100%;
				padding: 10px 15px;
				margin-bottom: 10px;
				font-size: 15px;
				text-align: center;
				text-transform: uppercase;
				border-color: #d7d7d7;
				border-radius: 10px;
				cursor: pointer;

				&:focus {
					outline: none;
				}
			}
			a {
				display: inline-flex;
				align-items: center;
				color: $blue;

				&:hover {
					text-decoration: none;
				}
				.icon {
					margin-left: 5px;
					color: $guava;
				}
			}
		}
	}
	&-footer {
		display: flex;
		justify-content: center;
		padding: 40px 15px;
		background-color: #001159;

		&--img {
			width: 150px;
		}
	}
	.disclaimer {
		font-size: 11px;
		text-align: center;

		// @media (min-width: 1200px) {
		// 	text-align: right;
		// }
	}
}

@media (max-width: 1199px) {
	.lp {
		.calendar {
			&--filter {
				justify-content: center;
			}
			&-item {
				&:nth-child(1),
				&:nth-child(3) {
					display: none;
				}
			}
		}
	}
}
@media (max-width: 991px) {
	.lp {
		&-banner {
			overflow: hidden;
		}
		.calendar {
			&--filter {
				.filter {
					&--events {
						margin: 0 -10px;

						.filter-item {
							padding: 10px;
						}
					}
				}
			}
			&--caption {
				flex-wrap: wrap;

				.caption--item {
					// flex: 0 0 50%;
				}
			}
		}
	}
}
@media (max-width: 767px) {
	.lp {
		&-header {
			padding-right: 15px;
			padding-left: 15px;
		}
		&-fav {
			right: 10px;
		}
		&-banner {
			&--txt {
				left: 15px;
				right: 15px;
			}
		}
		.calendar {
			&--filter {
				.filter {
					&--events {
						.filter-item {
							flex-direction: column;
							justify-content: flex-start;
							padding: 10px 15px;
							margin: 0 3px;
							text-align: center;

							img {
								margin-right: 0;
								margin-bottom: 5px;
							}
						}
					}
				}
			}
			&--btns {
				.btn-prev {
					left: 10px;
				}
				.btn-next {
					right: 10px;
				}
			}
		}
	}
}
@media (max-width: 575px) {
	.lp {
		&-banner {
			&--txt {
				h1 {
					font-size: 40px;
				}
				h2 {
					font-size: 20px;
				}
			}
		}
		.calendar {
			padding: 30px 0;

			&--filter {
				flex-wrap: wrap;

				.filter {
					&--events {
						flex-grow: 1;
						flex-wrap: wrap;
						align-items: flex-start;
						justify-content: center;

						.filter-item {
							padding: 12px;
							margin: 0;
						}
					}
					&-item {
						height: auto;
					}
				}
			}
			&-item {
				&:nth-child(2) {
					.fc-header-toolbar {
						padding-bottom: 10px;

						h2 {
							font-size: 26px;
						}
					}
				}
			}
			&--btns {
				.btn-prev,
				.btn-next {
					top: 40px;
					padding: 8px;
				}
			}
			&--caption {
				.caption--item {
					// flex: 0 0 100%;
				}
			}
		}
	}
}

@media (min-width: 576px) and (max-width: 1199px) {
	.lp {
		&-banner {
			.item {
				position: relative;
				min-height: 300px;
				overflow: hidden;

				img {
					position: absolute;
					top: 0;
					left: 50%;
					transform: translateX(-50%);
					display: inline-block !important;
					width: auto !important;
					height: 100%;
				}
			}
		}
	}
}

@media (min-width: 1200px) {
	.lp-fav {
    top: 5px;
    padding: 10px;
    text-align: right;
    border-radius: 14px;
    width: 576px;
    height: 86px;
    padding-right: 78px;
    max-width: 576px;

		&--header {
			height: 31px;
		}
	}
}