.ecom {
  min-height: 300px;
  color: white;
  background-image: url("../img/bg-ecom.png");
  background-size: 100% auto;
  // overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 15px;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
        max-width: 700px;
      }
    }
  }
  &-list {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    .list-item {
      position: relative;
      font-size: 15px;
      text-align: center;

      .item {
        &-img {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          height: 65px;
        }
      }
      p {
        margin-bottom: 5px;
      }
      .info {
        position: absolute;
        top: 100%;
        right: 0;
        display: none;
        flex-direction: column;
        align-items: center;

        @media (min-width: 1200px) {
          right: auto;
          left: 50%;
          transform: translateX(-50%);
        }
        &.show {
          display: flex;
        }
        &-text {
          width: 315px;
          padding: 15px;
          margin-top: -14px;
          color: $navy;
          font-size: 12px;
          font-weight: normal;
          text-align: left;
          background-color: $guava;
          border-radius: 14px;

          @media (min-width: 1300px) {
            width: 400px;
          }
        }
        .icon {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 28px;
          height: 28px;
          margin: 0;
          color: $navy;
          font-size: 20px;
          background-color: $guava;
          border-radius: 100%;

          &:before {
            left: -1px;
          }
        }
      }
      // &:not(:last-child) {
      //   .item-img {
      //     &:after {
      //       content: "\e020";
      //       position: absolute;
      //       top: 50%;
      //       right: 0;
      //       transform: translateY(-50%);
      //       color: white;
      //       font-size: 18px;
      //       font-family: $font-icons;

      //       @media (min-width: 576px) {
      //       }
      //       @media (min-width: 768px) {
      //         right: auto;
      //         left: calc(100% + 2vw);
      //       }
      //       @media (min-width: 992px) {
      //         left: calc(100% + 25px);
      //         font-size: 25px;
      //       }
      //     }
      //   }
      // }
    }
    >.icon {
      margin-top: 20px;
      font-size: 25px;
    }
  }
}
.monitor {
  padding: 60px 0;

  >.container {
    @media (min-width: 1300px) {
      max-width: 1250px;
    }

    .col-24 {
      padding: 15px 15px 30px;
    }
  }
  &-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 15px 25px;
    color: white;
    font-size: 15px;
    text-align: center;
    background-color: $navy;
    border-radius: 14px;

    .box-title {
      margin: 25px 0;
      color: $guava;
      font-size: 36px;
      font-weight: 300;
      line-height: 1;
    }
    a {
      color: $guava;
      text-decoration: underline;
    }
  }
}
@media (max-width: 1199px) {
  .ecom {
    background-size: auto 100%;

    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
    &-list {
      .list-item {
        .info {
          position: fixed;
          top: 50vh;
          left: 50vw;
          transform: translate(-50%, -50%);
          z-index: 1041;
          width: calc(100% - 30px);
          max-width: 400px;

          &-text {
            width: 100%;
          }
          .icon {
            &:before {
              content: "\E011";
              transform: rotate(45deg);
            }
          }
        }
        &.open {
          &:after {
            content: "";
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1040;
            width: 100%;
            height: 100%;
            background-color: rgba($navy, .9);
          }
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .ecom {
    &-list {
      >.icon {
        font-size: 20px;
      }
    }
  }
  .monitor {
    padding-top: 30px;

    >.container {
      >.row {
        flex-direction: column-reverse;
      }
    }
  }
}
@media (max-width: 767px) {
  .ecom {
    &--title {
      font-size: 32px;
    }
    .row {
      min-height: 300px;

      .col-24 {
        &:last-child {
          max-width: none;
        }
      }
    }
    &-list {
      flex-wrap: wrap;
      justify-content: flex-start;

      .list-item {
        flex: 0 0 28%;
      }
      >.icon {
        font-size: 25px;
      }
    }
  }
}
@media (max-width: 575px) {
  .ecom {
    &-list {
      .list-item {
        flex: 0 0 40%;
      }
      >.icon {
        font-size: 20px;
      }
    }
  }
}