.intro {
  padding: 60px 0;
  text-align: center;

  &--title {
    margin-top: 0;
    margin-bottom: 40px;
    color: $blue;
    font-size: 50px;
    font-weight: 300;
  }
  &--subtitle {
    color: $blue;
  }
  .color {
    color: $blue;
  }
  .icon-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 0;
    margin-bottom: 25px;
    list-style: none;

    li {
      flex: 0 0 16.6666%;
      padding: 15px;
      text-align: center;

      img {
        height: 72px;
        margin-bottom: 10px;
      }
      p {
        margin-bottom: 0;
        color: $guava;
        white-space: nowrap;
      }
    }
    &__navios {
      li {
        flex: 0 0 34%;

        @media (max-width: 1199px) {
          flex: 0 0 50%;
        }
        @media (max-width: 767px) {
          flex: 0 0 100%;
        }
        h3 {
          color: $navy;
          font-size: 32px;
          font-weight: 300;
        }
        p {
          color: $font-color;
          white-space: normal;
        }
      }
    }
    &__servicos {
      margin: 0 -15px 25px;

      li {
        flex: 0 0 33.3333%;

        @media (max-width: 1199px) {
          flex: 0 0 50%;
        }
        @media (max-width: 767px) {
          flex: 0 0 100%;
        }

        a {
          display: block;

          &:hover {
            text-decoration: none;

            h3 {
              color: $guava;
            }
          }
        }
        img {
          height: 65px;
        }
        h3 {
          color: $navy;
          font-size: 28px;
          font-weight: 300;
        }
        p {
          color: $font-color;
          white-space: normal;
        }
      }
    }
  }
  .bullet-list {
    padding-left: 5px;
    margin-bottom: 0;
    list-style: none;
    text-align: left;

    li {
      margin-bottom: 15px;

      &:before {
        content: "";
        display: inline-block;
        width: 11px;
        height: 11px;
        margin-right: 15px;
        background-color: $blue;
        border-radius: 100%;
      }
    }
  }
  a {
    color: $blue;
  }
  &--herotitle {
    margin-top: -30px;

    img {
      width: 100%;
    }
  }
  &.bg {
    background-image: url("../img/bg-noticias.png");
    background-size: 100% auto;
    background-position: bottom left;
    background-repeat: no-repeat;

    @media (max-width: 767px) {
			background-image: none;
		}
  }
}

.breadcrumb-wrap + .intro {
  padding-top: 0;
}

@media (max-width: 767px) {
  .intro {
    &--title {
      font-size: 36px;
    }
    &--herotitle {
      position: relative;
      height: 200px;
      overflow: hidden;

      img {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: auto;
        height: 100%;
      }
    }
  }
}