.faq {
  padding: 60px 0;

  &--filter {
    display: flex;
    max-width: 400px;
    margin: 30px auto 40px;

    .form-control {
      border-color: $navy;
      border-radius: 14px;
    }
    .btn {
      padding: 10px 15px;
      margin-left: 15px;
    }
  }
  &--title {
    color: $navy;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
  }
  &--list {
    counter-reset: counter;
  }
  &--item {
    position: relative;
    padding: 10px 15px;
    margin-bottom: 15px;
    border-radius: 14px;
    box-shadow: 0 0 10px rgba(#282828, .1);
    transition: background-color .3s;
    counter-increment: counter;

    // &:before {
    //   content: "";
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   z-index: 0;
    //   width: 100%;
    //   height: 100%;
    //   background-color: #e7e7e7;
    //   border-radius: 14px;
    //   opacity: 0;
    //   transition: opacity .4s;
    // }
    &.open {
      background-color: #e7e7e7;
    }
  }
  &--link {
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    color: $font-color;

    p {
      display: flex;
      align-items: center;
      position: relative;
      margin-bottom: 0;
      font-size: 20px;
      line-height: 1.3;
      min-height: 32px;

      &:before {
        content: counter(counter);
        flex: 0 0 32px;
        align-self: flex-start;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        margin-right: 15px;
        color: white;
        background-color: $font-color;
        border-radius: 100%;
      }
    }
    .icon {
      padding: 8px 0;
      font-size: 20px;
      transition: transform .2s;
    }
    &[aria-expanded="true"],
    &:hover {
      color: $blue;

      p {
        &:before {
          background-color: $blue;
        }
      }
    }
    &[aria-expanded="true"] {
      .icon {
        transform: rotate(180deg);
      }
    }
    &:hover {
      text-decoration: none;
    }
  }
  &--collapse {
    position: relative;
    color: #1e1e1e;
    font-size: 18px;

    p {
      margin-bottom: 0;
    }
  }
  &--wrap {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 47px;
  }
}