.carreira {
  min-height: 300px;
  color: white;
  background-image: url("../img/bg-carreira.jpg");
  background-size: 100% auto;
  // overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 30px;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
        max-width: 700px;
      }
    }
  }
  ul {
    display: flex;
    flex-wrap: wrap;
    padding-left: 0;
    margin: 0 -15px;
    list-style: none;

    li {
      flex: 0 0 50%;
      display: flex;
      padding: 0 15px 2px;

      &:before {
        content: "";
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 10px;
        height: 10px;
        min-width: 10px;
        margin-top: 8px;
        margin-right: 12px;
        font-size: 20px;
        background-color: $blue;
        border-radius: 100%;
      }
      .info {
        position: relative;
        display: flex;
        align-items: center;
        align-self: flex-start;
        justify-content: center;
        width: 14px;
        height: 14px;
        min-width: 14px;
        margin-left: 2px;
        color: $navy;
        font-size: 12px;
        font-weight: bold;
        background-color: $guava;
        border-radius: 100%;
        // cursor: pointer;

        &-txt {
          position: absolute;
          top: 15px;
          left: 50%;
          transform: translateX(-50%);
          z-index: 2;
          display: none;
          width: 400px;
          padding: 15px;
          color: $navy;
          font-size: 12px;
          font-weight: normal;
          background-color: $guava;
          border-radius: 14px;

          h3 {
            position: absolute;
            top: -15px;
            left: 50%;
            transform: translateX(-50%);
            display: inline-flex;
            align-items: center;
            justify-content: center;
            width: 28px;
            margin: 0;
            font-size: 22px;
            background-color: $guava;
            border-radius: 100%;
          }

          &.show {
            display: block;
          }
        }
      }
    }
  }

  &-mapa {
    padding: 20px 0 50px;
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-53%);
      width: 100%;
      height: 38%;
      background-color: #e7e7e7;

      @media (max-width: 767px) {
        height: 100%;
      }
    }
    .container {
      @media (min-width: 1500px) {
        max-width: 1400px;
      }
      @media (max-width: 1199px) {
        max-width: none;
      }
    }
    .row {
      align-items: center;

      @media (max-width: 767px) {
        flex-direction: column-reverse;
        text-align: center;
      }
    }
    &--title {
      margin: 20px 0;
      color: $blue;
      font-size: 36px;
      font-weight: 300;

      @media (min-width: 768px) and (max-width: 991px) {
        margin: 10px 0;
        font-size: 30px;
      }
    }
    &--subtitle {
      margin: 20px 0;
      color: $guava;
      font-size: 36px;
      font-weight: 300;

      @media (min-width: 768px) and (max-width: 991px) {
        margin: 10px 0;
        font-size: 30px;
      }
    }
    p {
      margin-bottom: 0;
    }
  }
}
@media (max-width: 1199px) {
  .carreira {
    background-size: auto 100%;

    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
    ul {
      li {
        .info {
          &-txt {
            top: 15px;
            left: auto;
            right: 0;
            transform: none;

            h3 {
              right: -8px;
              left: auto;
              transform: none;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 575px) {
  .carreira {
    &--title {
      font-size: 30px;
    }
    ul {
      li {
        position: relative;
        flex: 0 0 100%;
        display: flex;

        .info {
          position: static;

          &-txt {
            top: 0;
            left: 15px;
            right: 15px;
            transform: none;
            width: calc(100vw - 30px);

            h3 {
              right: 50%;
              transform: translateX(50%);
            }
          }
        }
      }
    }
  }
}