.depoimentos {
  padding: 60px 0;

  &-carousel.owl-carousel {
    padding: 0 45px;

    .item {
      display: flex;
      align-items: flex-end;
      flex-wrap: wrap;

      &-txt {
        position: relative;
        padding: 0 70px;
        margin-bottom: 20px;
        // color: $navy;
        font-size: 16px;
        font-style: italic;
        font-weight: 300;

        &:before,
        &:after {
          content: url("../img/quote.jpg");
          position: absolute;
        }
        &:before {
          top: 0;
          left: 0;
        }
        &:after {
          right: 0;
          bottom: 0;
          transform: rotate(180deg);
        }
      }
      &-signature {
        padding: 0 70px;
        margin-bottom: 0;
        color: $navy;
        font-size: 18px;
        font-weight: 300;

        strong {
          display: block;
          color: $blue;
          font-size: 24px;
          font-weight: bold;
          line-height: 1;
        }
      }
    }
    .owl-nav {
      @media (max-width: 991px) {
        display: none;
      }
      .owl-prev,
      .owl-next {
        position: absolute;
        top: 50%;
        transform: translateY(calc(-50% - 100px));
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        color: white;
        font-size: 25px;
        background-color: $blue;
        border-radius: 100%;
        transition: all .3s;

        &:focus {
          outline: none;
        }
        &:hover {
          background-color: $navy;
        }
      }
      .owl-prev {
        left: -2vw;
      }
      .owl-next {
        right: -2vw;
      }
    }
    .owl-dots {
      display: flex;
      align-items: center;
      justify-content: center;
      padding-top: 50px;

      .owl-dot {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 18px;
        height: 18px;
        margin: 8px;
        background-color: white;
        border-radius: 100%;


        span {
          display: block;
          width: 12px;
          height: 12px;
          border: 2px solid $font-color;
          border-radius: 100%;
          transition: all .3s;
        }
        &.active {
          span {
            width: 18px;
            height: 18px;
            border-color: $blue;
            border-width: 3px;
          }
        }
        &:not(:last-child) {
          &:after {
            content: "";
            position: absolute;
            top: 50%;
            left: 100%;
            transform: translate(3px, -50%);
            width: 10px;
            height: 2px;
            background-color: $font-color;
          }
        }
      }
    }
  }
}
@media (min-width: 1500px) {
  .depoimentos {
    &-carousel.owl-carousel {
      .owl-nav {
        .owl-prev {
          left: -10vw;
        }
        .owl-next {
          right: -10vw;
        }
      }
    }
  }
}
@media (max-width: 1199px) {
  .depoimentos {
    >.container {
      max-width: none;
    }
    &-carousel.owl-carousel {
      .owl-nav {
        .owl-prev {
          left: 0;
        }
        .owl-next {
          right: 0;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .depoimentos {
    padding: 40px 0;

    &-carousel.owl-carousel {
      padding: 0;
    }
  }
}
@media (max-width: 575px) {
  .depoimentos {
    &-carousel.owl-carousel {
      .item {
        &-txt {
          padding: 50px 0;
        }
        &-signature {
          padding: 0;
        }
      }
    }
  }
}