.rodoviario {
  min-height: 300px;
  color: white;
  background-image: url("../img/rodoviario.jpg");
  background-size: 100% auto;
  overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 20px;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  a {
    color: $guava;
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
        max-width: 800px;
      }
    }
  }
  &--frota {
    padding-top: 60px;
    text-align: center;

    .frota{
      &--items {
        padding: 60px 0 40px;
        text-align: left;
        background-image: url("../img/bg-category.png");
        background-size: 100% auto;
        background-position: top left;
        background-repeat: no-repeat;

        @media (max-width: 767px) {
          background-image: none;
        }

        .item {
          margin-bottom: 20px;

          &-img {
            margin-bottom: 15px;

            img {
              width: 100%;
            }
          }
          &-title {
            color: $blue;
            font-size: 32px;
            font-weight: 300;
          }
          .blue {
            color: $blue;
          }
        }
      }
    }
  }
}
@media (max-width: 1199px) {
  .rodoviario {
    background-size: auto 100%;

    &--title {
      font-size: 32px;
    }
    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
  }
}