.banner-interna {
  position: relative;
  overflow: hidden;

  img {
    width: 100%;
  }
}

@media (max-width: 767px) {
  .banner-interna {
    height: 200px;
    text-align: right;

    img {
      position: absolute;
      top: 0;
      right: 0;
      width: auto;
      height: 100%;
    }
  }
}