.cookies {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 100;
  transform: translateY(100%);
  display: block;
  width: 100%;
  padding: 15px 0;
  background-color: rgba($blue, .9);
  transition: transform .4s;

  &.open {
    transform: translateY(0);
  }
  .container-fluid {
    max-width: 800px;
  }
  p {
    color: white;
    font-size: 13px;
    text-align: center;
  }
  &--links {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    a {
      padding: 2px 10px;
      color: white;
      font-size: 14px;
      font-weight: 600;
    }
  }
}
.modal-cookies {
  color: white;

  .modal-dialog {
    @media (min-width: 768px) {
      max-width: 600px;
    }
  }
  .modal-content {
    padding: 30px 25px 30px;
    background-color: $blue;
    border-radius: 10px;
  }
  .close {
    position: absolute;
    top: 10px;
    right: 10px;
    transform: rotate(45deg);
    color: white;
    font-size: 30px;
    opacity: 1;

    &:focus {
      outline: none;
    }
  }
  .modal-title {
    margin-bottom: 20px;
    font-size: 25px;
    font-weight: 300;
    text-transform: uppercase;
  }

  .switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 30px;
    min-width: 50px;
    margin-right: 10px;
    margin-bottom: 0;

    input {
      opacity: 0;
      width: 0;
      height: 0;

      &:checked + .slider {
        background-color: $navy;
      }
      &:focus + .slider {
        box-shadow: 0 0 1px $navy;
      }
      &:checked + .slider:before {
        -webkit-transform: translateX(22px);
        -ms-transform: translateX(22px);
        transform: translateX(22px);
      }
    }
  }
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;

    &:before {
      position: absolute;
      content: "";
      height: 22px;
      width: 22px;
      left: 4px;
      bottom: 4px;
      background-color: white;
      -webkit-transition: .4s;
      transition: .4s;
    }
    &.round {
      border-radius: 34px;

      &:before {
        border-radius: 50%;
      }
    }
  }
}