.modal-search {
	.modal {
		&-dialog {
			margin-top: 90px;
		}
		&-content {
			padding: 0 15px;
			background-color: transparent;
			border: none;
		}
		&-title {
			margin-bottom: 20px;
			color: white;
			font-size: 50px;
			font-weight: 300;
			line-height: 1;
		}
	}
	.form {
		&-group {
			position: relative;
			margin-bottom: 0;
		}
		&-control {
			height: 50px;
			padding-right: 50px;
			border: none;
			border-radius: 0;
		}
	}
	.close {
		position: absolute;
		top: 5px;
		right: 16px;
		color: white;
		font-size: 18px;
		opacity: 1;
	}
	.btn-search {
		position: absolute;
		top: 0;
		right: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		height: 50px;
		color: $blue;
		font-size: 30px;
		background-color: white;
		border: none;
		cursor: pointer;
	}
	.result {
		color: white;
		margin-top: 30px;

		&--list {
			list-style: none;
			padding-right: 10px;
			margin-bottom: 0;
			padding-left: 0;
			max-height: calc(100vh - 325px);
			overflow-y: auto;

			&::-webkit-scrollbar {
			  width: 5px;
			}
			&::-webkit-scrollbar-track {
			  background: $blue;
			  border: 4px solid $navy;
			}
			&::-webkit-scrollbar-thumb {
			  background: $blue;
			  border-radius: 5px;
			}
		}
		&--item {
			border-bottom: 1px solid white;

			&:not(:first-child) {
				margin-top: 1rem;
			}
			a {
				display: block;
				color: white;

				&:hover {
					text-decoration: none;

					.result--title {
						text-decoration: underline;
					}
				}
			}
			p {
				font-size: 15px;
				line-height: 1.2;
			}
		}
		&--title {
			margin-bottom: 5px;
			color: white;
			font-size: 20px;
		}
	}
}
.modal-backdrop {
	background-color: $navy;

	&.show {
  		opacity: 0.9;
  	}
}

@media (min-width: 992px) {
	.modal-search {
		.modal-dialog {
			max-width: 700px;
		}
	}
}
@media (max-width: 991px) {
	.modal-search {
		.modal-dialog {
			margin-top: 40px;
		}
		.modal-title {
			font-size: 36px;
		}
		.result--list {
			max-height: calc(100vh - 225px);
		}
	}
}