.servicos {
  min-height: 300px;
  color: white;
  background-image: url("../img/servicos.png");
  background-size: 100% auto;
  overflow: hidden;

  .container-fluid {
    min-height: 300px;
  }
  &--title {
    margin-bottom: 20px;
    color: $guava;
    font-size: 36px;
    font-weight: 300;
    line-height: 1;
  }
  .img-responsive {
    margin-bottom: 0;
  }
  p {
    &:last-child {
      margin-bottom: 0;
    }
  }
  .row {
    min-height: 300px;

    .col-24 {
      &:last-child {
        align-self: center;
        padding: 30px 15px;
        max-width: 800px;
      }
    }
  }
  &-exemplos {
    padding: 60px 0;
    background-color: #f5f5f5;

    .exemplos {
      &--title {
        margin-bottom: 40px;
        color: $navy;
        font-size: 36px;
        font-weight: 300;
        text-align: center;
      }
      &--item {
        padding: 15px 45px;
        margin-bottom: 30px;
        background-color: white;
        border-radius: 14px;
        box-shadow: 0 0 10px rgba(#282828, .1);

        .item {
          &-link {
            display: flex;
            align-items: center;
            color: $blue;
            font-size: 20px;
            font-weight: 300;

            &:hover {
              color: $guava;
              text-decoration: none;
            }
            img {
              flex: 0 0 80px;
              margin-right: 15px;
            }
          }
          &-collapse {
            p:first-child {
              padding-top: 15px;
            }
          }
        }
        .blue {
          color: $blue;
        }
        .guava {
          color: $guava;
        }
      }
    }
  }
}
@media (max-width: 991px) {
  .servicos {
    background-size: auto 100%;

    &--title {
      font-size: 32px;
    }
    .img-responsive {
      position: relative;
      width: 100%;
      height: 100%;

      img {
        position: absolute;
        top: 0;
        right: 0;
        width: auto;
        height: 100%;
        max-width: none;
      }
    }
  }
}