.calendario {
  position: relative;
  min-height: 450px;
  color: white;
  overflow: hidden;

  .container {
    position: absolute;
    top: 10%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    text-align: center;

    @media (min-width: 992px) {
      max-width: 863px;
    }
  }
  img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: auto;
  }
  &--title {
    margin-bottom: 25px;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
    line-height: 1.3em;
  }
  p {
    margin-bottom: 40px;
    font-size: 18px;
  }
  .btn {
    min-width: 150px;
    padding: 10px 25px;
    text-transform: uppercase;
  }
}

@media (max-width: 1199px) {
  .calendario {
    img {
      left: 50%;
      transform: translateX(-50%);
      width: auto;
      height: 100%;
    }
  }
}
@media (max-width: 767px) {
  .calendario {
    .container {
      max-width: 100%;
    }
  }
}
@media (max-width: 575px) {
  .calendario {
    min-height: 400px;

    &--title {
      margin-bottom: 20px;
      font-size: 22px;
    }
    p {
      margin-bottom: 30px;
      font-size: 16px;
    }
    .btn {
      min-width: 130px;
      padding: 8px 25px;
      text-transform: uppercase;
    }
  }
}